import * as React from 'react';
import { Container } from 'reactstrap';
import { Button } from 'reactstrap';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../../store/Station';

type StationsManagerProps = {
    stationControllers: StationStore.StationController[],
    isLoading: boolean,
    handleSetTraceabilityModeClick: (stationControllerId: string, newState: boolean) => void
}

class StationsManagerView extends React.PureComponent<StationsManagerProps> {
    public render() {
        const getListOptionsStationControllers = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            let count = this.props.stationControllers.length;
            if (count === 0) {
                count = this.props.stationControllers.reduce(((total, x) =>
                    total + x.stations.length
                ), 0);
            }

            if (count === 0) {
                return "No stations controllers / stations  connected.";
            }

            let options =
                this.props.stationControllers
                    .map(sc => {
                        let items = [];
                        items.push(
                            <tr className="level-1">
                                <td className="text-left"><strong>{sc.name}</strong></td>
                                <td></td>
                                <td>
                                    <Button
                                        outline
                                        color={(sc.isQSTMode ? "primary" : "secondary")}
                                        size="sm"
                                        className="pt-0 pb-0"
                                        onClick={() => this.props.handleSetTraceabilityModeClick(sc.id, !sc.isQSTMode)}
                                    >
                                        {sc.isQSTMode ? "Enabled" : "Disabled"}
                                    </Button>
                                </td>
                            </tr>
                        )
                        items.push(
                            sc.stations.map(s => (
                                <tr className="level-2">
                                    <td className="pl-3 text-left">{s.name.length === 0 ? "[No name]" : s.name}</td>
                                    <td>{s.model}</td>
                                    <td>
                                        <span className={(sc.isQSTMode && s.isQSTMode) ? "highlight-color" : ""}>{
                                            sc.isQSTMode ?
                                                (s.isQSTMode ? "QST enabled" : "QST disabled") :
                                                "--"
                                        }</span>
                                    </td>
                                </tr>
                            ))
                        )
                        return items;
                    })

            return (
                <table className="table table-hover table-sm station-list">
                    <thead>
                        <tr>
                            <th scope="col">Station Controller / Station</th>
                            <th scope="col">Station model</th>
                            <th scope="col">QST state</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        };

        const getListOptionsStationControllersCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            {getListOptionsStationControllers()}
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        return (
            <Container>
                <Row className="mb-2">
                    <h1>Stations Manager</h1>
                </Row>
                <Row>
                    {getListOptionsStationControllersCard()}
                </Row>
            </Container>
        );
    }
};

export default StationsManagerView;
