import * as React from 'react';
import { Container } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import { Button, NavLink } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import * as TaskStore from '../../../../store/Task';
import * as TaskHistoryStore from '../../../../store/TaskHistory';
import { Round } from '../../../../utils/numberUtils';
import { ToolToString } from '../../../../utils/toolUtils';
import { GetPlotSeriesData } from '../../../../utils/plotUtils';
import { ClassificationToString } from '../../../../utils/solderingClassificationUtils';
import Plot2Axis from '../../../content-parts/Plot2Axis';
import PlotPowerAxisTiny from '../../../content-parts/PlotPowerAxisTiny';
import PlotReferenceTiny from '../../../content-parts/PlotReferenceTiny';
import ResponsivePieChart from '../../../content-parts/ResponsivePieChart';
import './TaskHistoryDetail.css';

type TaskHistoryDetailProps = {
    task: TaskStore.Task,
    taskHistory: TaskHistoryStore.TaskHistory
}

class TaskHistoryDetailView extends React.PureComponent<TaskHistoryDetailProps> {
    public render() {
        const getDetailsCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>Finished date: {new Date(this.props.taskHistory.finishDate).toLocaleDateString('es-ES')}</CardText>
                        <CardText>Task code: {this.props.task.id}</CardText>

                        <hr />
                        <CardText>Station name: {this.props.taskHistory.workData.stationName}</CardText>
                        <CardText>Port: {this.props.taskHistory.workData.stationPort + 1}</CardText>

                        <hr />
                        <CardText>Tool: {ToolToString(this.props.taskHistory.workConfig.tool)}</CardText>
                        {this.props.taskHistory.workConfig.changeSelectedTemp ?
                            <CardText>
                                Selected temperature: {Math.floor(this.props.taskHistory.workConfig.selectedTemp / 9)} C
                            </CardText> :
                            ""
                        }
                        {this.props.taskHistory.workConfig.changeSolderingReference ?
                            <CardText>
                                Soldering reference: {this.props.taskHistory.workConfig.solderingReference.name}
                            </CardText> :
                            ""
                        }
                    </CardBody>
                </Card>
            )
        }

        const getDefectsChart = (correct: number, incorrect: number) => {
            const data = [
                { name: 'Correct', value: correct },
                { name: 'Incorrect', value: incorrect }
            ];

            return <ResponsivePieChart data={data} />
        }

        const getQualityCard = () => {
            let workSolderings = this.props.taskHistory.workData.workSolderings;

            let correct = workSolderings.filter(x => x.solderingType === 0).length;
            let incorrect = workSolderings.length - correct;

            let total = correct + incorrect;
            let percent = total === 0 ? 0 : Round((correct / total) * 100);

            return (
                <Card>
                    <CardBody>
                        {getDefectsChart(correct, incorrect)}
                        <CardText>Correct solderings: <strong>{correct}/{total} ({percent}%)</strong></CardText>
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredDataCard = () => {
            let plotData = GetPlotSeriesData(this.props.taskHistory.workData.temperature, this.props.taskHistory.workData.power);

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Registered data</CardTitle>
                        <CardText className="p-3">
                            <Plot2Axis
                                plotData={plotData}
                                counterTenthSecond={plotData.length}
                                interval={Math.floor(plotData.length / 5)}
                            />
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const getSolderingsCard = () => {
            let workSolderings = this.props.taskHistory.workData.workSolderings;
            let showReference = this.props.taskHistory.workConfig.changeSolderingReference;
            let referencePower = showReference ?
                this.props.taskHistory.workConfig.solderingReference.power : [];

            let registeredSolderings = workSolderings.map(x => {
                let circleLight = "circle-light-" + (x.solderingType === 0 ? "0" : "1")

                return (
                    <Col>
                        <Card className="card-v2">
                            <CardBody>
                                {
                                    this.props.task.workConfig.qualityControl ?
                                        <CardText>
                                            Detected defect: <FontAwesomeIcon icon={faCircle} className={circleLight + " pl-1"} />
                                            <strong className="pl-1">{ClassificationToString(x.solderingType)}</strong>
                                        </CardText> : ""
                                }
                                {
                                    showReference ?
                                        <PlotReferenceTiny
                                            soldering={x.power}
                                            reference={referencePower}
                                        /> :
                                        <PlotPowerAxisTiny
                                            plotData={x.power}
                                        />
                                }
                            </CardBody>
                        </Card>
                    </Col>
                );
            });

            return (
                <Card>
                    <CardBody>
                        <CardTitle>Detected solderings</CardTitle>
                        <Row xs={1} sm={2} md={4}>
                            {registeredSolderings}
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getLayout = () => {
            if (this.props.task.workConfig.qualityControl) {
                return (
                    <React.Fragment>
                        <Col xs="8" className="pl-0">
                            {getDetailsCard()}
                        </Col>
                        <Col xs="4" className="pl-0 pr-0">
                            {getQualityCard()}
                        </Col>
                    </React.Fragment>
                )
            }
            else {
                return (
                    <React.Fragment>
                        {getDetailsCard()}
                    </React.Fragment>
                )
            }
        }

        return (
            <Container>
                <Row className="mb-1">
                    <h1>{this.props.task.name}</h1>
                </Row>
                <Row className="mb-3">
                    {getLayout()}
                </Row>
                <Row className="mb-3">
                    {getSolderingsCard()}
                </Row>
                <Row>
                    {getRegisteredDataCard()}
                </Row>
                <Row>
                    <Button color="primary" size="sm" className="m-2">
                        <NavLink
                            className="text-white p-0"
                            href={"/qst/task/" + this.props.task.id + "/detail"}>Return</NavLink>
                    </Button>
                </Row>
            </Container>
        );
    }
};

export default TaskHistoryDetailView;
