import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class StationsService {
    static async getStations() {
        let stations;

        if (Configuration.FakeMode) {
            stations = FakeService.getStations();
        }
        else {
            stations = await RestService.getStations();
        }

        return stations;
    }

    static async setSelectedTemperature(stationControllerId, stationId, port, temperature) {
        if (Configuration.FakeMode) {
            FakeService.setSelectedTemperature();
        }
        else {
            await RestService.setSelectedTemperature(stationControllerId, stationId, port, temperature);
        }
    }

    static async setTraceabilityMode(stationControllerId, newState) {
        if (Configuration.FakeMode) {
            FakeService.setTraceabilityMode();
        }
        else {
            await RestService.setTraceabilityMode(stationControllerId, newState);
        }
    }
}

export default StationsService;
