import * as React from 'react';
import { Container } from 'reactstrap';
import { Button, Input, Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../../store/Station';
import * as StationUpdateStore from '../../../../store/StationUpdate';
import { IUpdateStation } from './data/IUpdateStation';

type StationsUpdateProps = {
    stationControllers: StationStore.StationController[],
    stationsUpdate: StationUpdateStore.StationUpdate[],
    isLoading: boolean,
    handleUpdateClick: (stationsToUpdate: IUpdateStation[]) => void,
}

interface IUpdateStationLanguage {
    stationId: string,
    language: string
}

interface IState {
    stationsToUpdate: IUpdateStation[],
    showUpdateButton: boolean,
    selectedLanguages: IUpdateStationLanguage[]
}

class StationsUpdateView extends React.PureComponent<StationsUpdateProps, IState> {
    constructor(props: StationsUpdateProps) {
        super(props);

        this.state = {
            stationsToUpdate: [],
            showUpdateButton: false,
            selectedLanguages: []
        };
    }

    public render() {
        const getListOptionsStationControllers = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            let count = this.props.stationControllers.reduce(((total, x) =>
                total + x.stations.length
            ), 0);

            if (count === 0) {
                return "No stations connected.";
            }

            let options =
                this.props.stationControllers
                    .map(sc => (
                        sc.stations.map(s => {
                            let softwareAvailable;
                            let updateAvailable = false;
                            let updates = this.props.stationsUpdate
                                .filter(x =>
                                    x.model === s.model &&
                                    (x.hardware === "" || x.hardware === s.hardware)
                                );

                            if (updates.length === 0) {
                                softwareAvailable = "No update available";
                            }
                            else if (updates.length === 1) {
                                let update = updates[0];

                                if (update.software === s.software) {
                                    softwareAvailable = "Updated";
                                }
                                else {
                                    softwareAvailable = update.software;
                                    updateAvailable = true;
                                }
                            }
                            else if (updates.length > 1) {
                                softwareAvailable =
                                    <Input type="select" onChange={handleSelectedLanguage}>
                                        {updates.map(x => 
                                            <option
                                                value={JSON.stringify({stationId: s.id, language: x.language})}
                                            >
                                                {x.software} ({x.language})
                                            </option>
                                        )}
                                    </Input>
                                updateAvailable = true;
                            }
                            
                            return (
                                <tr className={(updateAvailable ? "font-weight-bold" : "") + " pointer"}>
                                    <td>{
                                        updateAvailable ?
                                            <Input
                                                type="checkbox"
                                                onChange={() => handleStationClick(sc.id, s.id)}
                                            /> : ""
                                    }</td>
                                    <td>{s.name} [{s.model}]</td>
                                    <td>{s.software}</td>
                                    <td>{softwareAvailable}</td>
                                </tr>);
                        })
                    ))

            return (
                <table className="table table-hover table-sm">
                    <thead>
                        <tr>
                            <th scope="col"></th>
                            <th scope="col">Station name</th>
                            <th scope="col">Program version</th>
                            <th scope="col">Available program version</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            );
        };

        const getListOptionsStationControllersCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            {getListOptionsStationControllers()}
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const getButton = () => {
            return (
                <Button
                    color="primary"
                    size="sm"
                    className="m-2"
                    disabled={!this.state.showUpdateButton}
                    onClick={handleUpdateClick}
                >Update</Button>)
        }

        const handleSelectedLanguage = (event: React.ChangeEvent<HTMLInputElement>) => {
            let selectedLanguage = JSON.parse(event.target.value) as IUpdateStationLanguage;

            let selectedLanguages = this.state.selectedLanguages;
            selectedLanguages = selectedLanguages.filter(x => x.stationId !== selectedLanguage.stationId);
            selectedLanguages.push(selectedLanguage);

            this.setState({
                selectedLanguages: selectedLanguages
            });
        }

        const handleStationClick = (stationControllerId: string, stationId: string) => {
            let stationsToUpdate = this.state.stationsToUpdate;
            let exists = stationsToUpdate.find(x => x.stationId === stationId) !== undefined;

            if (exists) {
                stationsToUpdate = stationsToUpdate.filter(x => x.stationId !== stationId);
            }
            else {
                stationsToUpdate.push({
                    stationControllerId: stationControllerId,
                    stationId: stationId,
                    language: "English"
                });
            }

            this.setState({
                stationsToUpdate: stationsToUpdate,
                showUpdateButton: stationsToUpdate.length > 0
            });
        }

        const handleUpdateClick = () => {
            let stations = this.state.stationsToUpdate.map(x => {
                let selectedLanguage = this.state.selectedLanguages.find(lang => x.stationId === lang.stationId);
                let language = selectedLanguage === undefined ? "English" : selectedLanguage.language;

                x.language = language;
                return x;
            });

            this.props.handleUpdateClick(stations);
        }

        return (
            <Container>
                <Row>
                    <h1>Updates available</h1>
                </Row>
                <Row>
                    {getListOptionsStationControllersCard()}
                </Row>
                <Row>
                    {getButton()}
                </Row>
            </Container>
        );
    }
};

export default StationsUpdateView;
