import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Input } from 'reactstrap';

type ProcessOptimizationProps = {
    selectedOffsetTime: number,
    handleOffsetTimeChange: (value: string) => void
}

class ProcessOptimizationStep_ProcessParameters extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            <strong>PCB preparation time (seconds):</strong>
                            <Input
                                defaultValue={this.props.selectedOffsetTime}
                                onChange={(e) => this.props.handleOffsetTimeChange(e.target.value)} />
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row>
                    {getConfigurationCard()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_ProcessParameters;
