import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import * as StationStore from '../../../../store/Station';
import StationsPortList from '../../../content-parts/StationsPortList';

type ProcessOptimizationProps = {
    isLoadingStations: boolean,
    stationControllers: StationStore.StationController[],
    handleStationClick: (stationControllerId: string, station: StationStore.Station, port: number, tool: number) => void
}

class ProcessOptimizationStep_Station extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getListOptionsStationControllers = () => {
            let filtered = this.props.stationControllers.map(sc => {
                let stations = sc.stations.map(s => {
                    let ports = s.ports.filter(p => p.tool === 1 || p.tool === 2);
                    s.ports = ports;
                    return s;
                });
                sc.stations = stations;
                return sc;
            });

            return (
                <StationsPortList
                    isLoading={this.props.isLoadingStations}
                    stationControllers={filtered}
                    handleStationClick={this.props.handleStationClick}
                />
            );
        };

        const getStationsCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            {getListOptionsStationControllers()}
                        </CardText>
                    </CardBody>
                </Card>
            );
        }

        return (
            <React.Fragment>
                <Row>
                    {getStationsCard()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_Station;
