import * as React from 'react';
import { Container } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Col, Row, Button, CustomInput, Input } from 'reactstrap';
import { NavLink } from 'reactstrap';
import { Card, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import PlotPowerAxisTiny from '../../../content-parts/PlotPowerAxisTiny';
import { GetTools } from '../../../../utils/toolUtils';
import { TryParseInt } from '../../../../utils/numberUtils';

type TaskNewProps = {
    isLoading: boolean,
    reference: AssistantStore.Reference,

    handleRefSolderingChange: (id: number) => void,
    newTask: (task: TaskStore.Task) => void
}

interface IState {
    task: TaskStore.Task;
}

class TaskNewView extends React.PureComponent<TaskNewProps, IState> {
    constructor(props: TaskNewProps) {
        super(props);

        let task = {
            id: 0,
            name: "",
            workConfig: {
                tool: 1,
                selectedTemp: 350 * 9,
                changeSelectedTemp: false,
                solderingReferenceId: -1,
                changeSolderingReference: false,
                qualityControl: false
            },
            timesPerformed: 0
        }

        this.state = {
            task: task
        };
    }

    public render() {
        const getListTools = () => {
            let tools = GetTools();
            return (
                <Input type="select" onChange={(e) => handleToolChange(e.target.value)}>
                    {Object.entries(tools).map(([key, value]) => (
                        <option value={key}>{value}</option>
                    ))}
                </Input>
            )
        }

        const getReferenceSoldering = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            if (this.props.reference.id <= 0) {
                return "Soldering reference undefined.";
            }

            return (
                <PlotPowerAxisTiny
                    plotData={this.props.reference.power}
                />
            )
        }

        const handleNameChange = (value: string) => {
            let newTask = this.state.task;
            newTask.name = value;

            this.setState({
                task: newTask
            });
        }

        const handleToolChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            let newTask = this.state.task;
            newTask.workConfig.tool = nValue;

            this.setState({
                task: newTask
            });
        }

        const handleModifySelecTempChange = (value: boolean) => {
            let newTask = this.state.task;
            newTask.workConfig.changeSelectedTemp = value;

            this.setState({
                task: newTask
            });
        }

        const handleSelecTempChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            nValue *= 9;

            let newTask = this.state.task;
            newTask.workConfig.selectedTemp = nValue;

            this.setState({
                task: newTask
            });
        }

        const handleModifySolderingRefChange = (value: boolean) => {
            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        changeSolderingReference: value
                    }
                }
            }));
        }

        const handleSolderingRefChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            this.props.handleRefSolderingChange(nValue);

            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        solderingReferenceId: nValue
                    }
                }
            }));
        }

        const handleQualityControlChange = (value: boolean) => {
            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        qualityControl: value
                    }
                }
            }));
        }

        const handleConfirmClick = () => {
            this.props.newTask(this.state.task);
        };

        return (
            <Container>
                <Row className="mb-1">
                    <h1>New task</h1>
                </Row>
                <Row>
                    <Card>
                        <CardBody>
                            <Row>
                                <Col xs="8">
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">Name:</Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                onChange={(e) => handleNameChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">Tool:</Col>
                                        <Col>{getListTools()}</Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeSelectedTemp"
                                                label="Selected temperature"
                                                onChange={(e) => handleModifySelecTempChange(e.target.checked)} />
                                        </Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                pattern="[0-9]*"
                                                onChange={(e) => handleSelecTempChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeSolderingReference"
                                                label="Soldering reference code"
                                                onChange={(e) => handleModifySolderingRefChange(e.target.checked)} />
                                        </Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                onChange={(e) => handleSolderingRefChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeQualityControl"
                                                label="Visualize quality control"
                                                onChange={(e) => handleQualityControlChange(e.target.checked)} />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xs="4">
                                    {getReferenceSoldering()}
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <Button outline color="primary" size="sm" className="m-2" onClick={() => handleConfirmClick()}>
                        Create
                    </Button>

                    <Button color="primary" size="sm" className="m-2">
                        <NavLink
                            tag={Link}
                            className="text-white p-0"
                            to="/qst/task">Cancel</NavLink>
                    </Button>
                </Row>
            </Container>
        );
    }
};

export default TaskNewView;
