import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../../store/Station';
import { ToolToString } from '../../../../utils/toolUtils';

type WorkProcessProps = {
    isLoading: boolean,
    stationControllers: StationStore.StationController[],
    taskTool: number,
    handleStationClick: (stationControllerId: string, station: StationStore.Station, port: number) => void
}

class WorkProcessStep2 extends React.PureComponent<WorkProcessProps> {
    public render() {
        const getListOptionsStationControllers = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            let stationControllers = this.props.stationControllers
                .filter(sc => (
                    sc.stations.some(s => (
                        s.ports.some(p => (
                            p.tool === this.props.taskTool
                        ))
                    ))
                ));

            let count = stationControllers.reduce(((total, x) =>
                total + x.stations.length
            ), 0);

            if (count === 0) {
                return "No stations connected.";
            }

            let options =
                stationControllers
                    .map(sc => (
                        sc.stations.map(s => (
                            s.ports
                                .filter(p => (
                                    p.tool === this.props.taskTool
                                ))
                                .map(p => (
                                    <tr className="pointer" onClick={() => this.props.handleStationClick(sc.id, s, p.id)}>
                                        <td>{s.name}[{s.model}]</td>
                                        <td>{p.id + 1}</td>
                                        <td>{ToolToString(p.tool)}</td>
                                    </tr>
                                ))
                        ))
                    ))

            return (
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Station name</th>
                            <th scope="col">Port</th>
                            <th scope="col">Tool</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        };

        return (
            <React.Fragment>
                <Row>
                    <Card>
                        <CardBody>
                            <CardText>
                                {getListOptionsStationControllers()}
                            </CardText>
                        </CardBody>
                    </Card>
                </Row>
            </React.Fragment>
        );
    }
};

export default WorkProcessStep2;
