import * as React from 'react';
import { Container } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Button, InputGroup, InputGroupAddon, Input } from 'reactstrap';
import { NavLink } from 'reactstrap';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEye } from '@fortawesome/free-regular-svg-icons'
import { faPen } from '@fortawesome/free-solid-svg-icons'
import { faFile } from '@fortawesome/free-regular-svg-icons'
import * as TaskStore from '../../../../store/Task';
import PaginationNav from '../../../content-components/PaginationNav';

type TasksProps = {
    tasks: TaskStore.Task[],
    nTotalTasks: number,
    pageSize: number,
    pageNumber: number,
    isLoading: boolean,

    handlePageClick: (pageNumber: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void,
    handleSearchFieldChange: (text: string) => void
}

class TasksView extends React.PureComponent<TasksProps> {

    public render() {
        const getListOptionsTasks = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>
                );
            }

            if (this.props.nTotalTasks === 0) {
                return (
                    <React.Fragment>
                        <span>No tasks, </span><NavLink tag={Link} className="p-0 d-inline" to="/qst/task/new">create new</NavLink><span> one.</span>
                    </React.Fragment>);
            }

            const options = [];

            for (const value of this.props.tasks) {
                options.push(
                    <tr>
                        <td>{value.id}</td>
                        <td>{value.name}</td>
                        <td>{value.timesPerformed}</td>
                        <td>
                            <NavLink tag={Link} className="p-0 pr-3 d-inline" to={"/qst/task/" + value.id + "/detail"}>
                                <FontAwesomeIcon icon={faEye} className="fa" />
                            </NavLink>
                            <NavLink tag={Link} className="p-0 pr-3 d-inline" to={"/qst/task/" + value.id + "/edit"}>
                                <FontAwesomeIcon icon={faPen} className="fa" />
                            </NavLink>
                        </td>
                    </tr>)
            }

            return (
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Code</th>
                            <th scope="col">Name</th>
                            <th scope="col"># of times performed</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        }

        const getListOptionsTasksCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Search result ({this.props.nTotalTasks} elements)</CardTitle>
                        <CardText>
                            {getListOptionsTasks()}
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        return (
            <Container>
                <Row>
                    <h1>List of Tasks</h1>
                </Row>
                <Row className="mb-1">
                    <Button outline color="primary" size="sm" className="m-2">
                        <NavLink tag={Link} className="p-0" to="/qst/task/new">
                            <FontAwesomeIcon icon={faFile} /> Create new
                        </NavLink>
                    </Button>
                </Row>
                <Row className="mb-3">
                    <InputGroup>
                        <InputGroupAddon addonType="prepend">Search</InputGroupAddon>
                        <Input onChange={(e) => this.props.handleSearchFieldChange(e.target.value)} />
                    </InputGroup>
                </Row>
                <Row className="mb-3">
                    {getListOptionsTasksCard()}
                </Row>
                <Row>
                    <PaginationNav
                        totalElements={this.props.nTotalTasks}
                        pageSize={this.props.pageSize}
                        pageNumber={this.props.pageNumber}
                        handlePageClick={this.props.handlePageClick}
                        handlePreviousPageClick={this.props.handlePreviousPageClick}
                        handleNextPageClick={this.props.handleNextPageClick}
                    />
                </Row>
            </Container>
        );
    }
};

export default TasksView;
