import * as React from 'react';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis, CartesianGrid } from 'recharts';
import { SecToString } from '../../../utils/timeUtils';

type PlotProps = {
    soldering: number[],
    reference: number[]
}

class PlotReference extends React.PureComponent<PlotProps> {
    public render() {
        let plotDataObj = [];

        for (var i: number = 0; i < Math.max(this.props.soldering.length, this.props.reference.length); i++) {
            plotDataObj.push({
                tick: i,
                soldering: i < this.props.soldering.length ? this.props.soldering[i] : null,
                reference: i < this.props.reference.length ? this.props.reference[i] : null
            });
        }

        return (
            <ResponsiveContainer width='100%' aspect={2.5}>
                <LineChart
                    data={plotDataObj}
                    margin={{
                        left: -10,
                        right: 0
                    }}
                >
                    <CartesianGrid stroke="#e6e6e6" />

                    <XAxis
                        dataKey="tick"
                        height={50}
                        label={"Time (s)"}
                        tickFormatter={(tick) => SecToString(Math.floor(tick / 10))}
                        interval={Math.floor(plotDataObj.length / 5)} />
                    <YAxis />

                    <Line dataKey="soldering" type="monotone" dot={false} />
                    <Line dataKey="reference" type="monotone" dot={false} stroke="#bd8531" />
                </LineChart>
            </ResponsiveContainer>
        );
    }
};

export default PlotReference;
