export function Range(min, max, step = 1) {
    var values = [];
    
    for (let i = min; i <= max; i+=step) {
        values = values.concat(i);
    }

    return values;
}

export function RemoveDuplicates(values) {
    values = values.filter((item, index) => values.indexOf(item) === index);
    return values;
}
