import React from 'react'
import { Link } from 'react-router-dom';
import { Card, CardText, CardBody, CardTitle, CardImg } from 'reactstrap';
import { NavLink } from 'reactstrap';
import './Card.css'

type CardProps = {
    to: string,
    imgPath: string,
    cardText: string,
    cardTitle: string,
    alt: string
};

const CardLinkAppView = ({ to, imgPath, cardText, cardTitle, alt }: CardProps) => {
    return (
        <NavLink className="card-link-app" tag={Link} to={to}>
            <Card className="box-shadow-v2">
                <CardBody>
                    <CardImg src={imgPath} alt={alt} />
                    <CardText className="pt-4">{cardText}</CardText>
                    <CardTitle>{cardTitle}</CardTitle>
                </CardBody>
            </Card>
        </NavLink>
    )
}

export default CardLinkAppView;