import * as React from 'react';
import { connect } from 'react-redux';
import { ApplicationState } from '../../../../store';
import * as StationStore from '../../../../store/Station';
import StationService from '../../../../services/station.service';
import StationsManagerView from './StationsManagerView';

type StationsManagerProps =
    StationStore.StationState &
    typeof StationStore.actionCreators;

interface IState {
    isLoading: boolean
}

class StationsManager extends React.PureComponent<StationsManagerProps, IState> {
    constructor(props: StationsManagerProps) {
        super(props);
        this.state = {
            isLoading: true
        };

        this.searchStations(this.props);
    }

    searchStations(props: StationsManagerProps) {
        props.deleteAllStationControllers();
        this.setState({
            isLoading: true
        })

        StationService
            .getStations()
            .then(scs => {
                if (Array.isArray(scs)) {
                    scs.forEach((sc: StationStore.StationController) => {
                        props.createStationController(sc);

                        sc.stations.forEach((s: StationStore.Station) =>
                            props.createStation(s, sc.id)
                        )
                    })
                }

                this.setState({
                    isLoading: false
                })
            });
    }

    public render() {
        const handleSetTraceabilityModeClick = (stationControllerId: string, newState: boolean) => {
            StationService
                .setTraceabilityMode(stationControllerId, newState)
                .then(x => {
                    this.searchStations(this.props);
                });
        }

        return (
            <StationsManagerView
                stationControllers={this.props.stationControllers}
                isLoading={this.state.isLoading}
                handleSetTraceabilityModeClick={handleSetTraceabilityModeClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers
    };
};

export default connect(
    mapStateToProps,
    StationStore.actionCreators
)(StationsManager);
