import * as React from 'react';
import { Col, Row, Input } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import * as StationStore from '../../../../store/Station';
import { CartridgeImagePathByFullName } from '../../../../utils/cartridgeUtils';
import Plot2AxisTiny from '../../../content-parts/Plot2AxisTiny';
import PortStatusCard from '../../../content-parts/PortStatusCard';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';

type ProcessOptimizationProps = {
    isLoading: boolean,

    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTemperature: number,
    selectedCartridge: string,

    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],

    handleSelecTempChange: (temperature: string) => void,
    handleDeleteRealTimeData: (value: string) => void
}

class ProcessOptimizationStep_TempTest extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />;
        }

        const getConfigurationCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>;
            }
            else {
                cardBody =
                    <Row>
                        <Col xs="8">
                            <CardText>Selected cartridge: {this.props.selectedCartridge}</CardText>
                            <CardText>
                                <strong>Selected temperature:</strong>
                                <Input
                                    type="select"
                                    defaultValue={this.props.selectedTemperature / 9}
                                    onChange={(e) => this.props.handleSelecTempChange(e.target.value)}
                                >
                                    <option>425</option>
                                    <option>400</option>
                                    <option>375</option>
                                    <option>350</option>
                                    <option>325</option>
                                    <option>300</option>
                                </Input>
                            </CardText>
                        </Col>
                        <Col xs="4" className="text-center">
                            <img src={CartridgeImagePathByFullName(this.props.selectedCartridge)} alt={this.props.selectedCartridge} />
                        </Col>
                    </Row>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredPlots = () => {
            if (this.props.detectedSolderings.length === 0) {
                return (
                    <CardText>
                        Performs at least 2 solderings with different temperatures.
                    </CardText>
                )
            }

            let registeredPlots = this.props.detectedSolderings.map(x => {
                let data: IRealTimeData[];
                data = x.data.map(x => {
                    return {
                        temperature: x.temperature,
                        power: x.power,
                        toolStatus: x.toolStatus,
                        isPortEnabled: x.isPortEnabled,
                        isCalibrating: x.isCalibrating,
                        isSoldering: x.isSoldering,
                        tenthsOfSecondSoldering: x.tenthsOfSecondSoldering
                    }
                });

                return (
                    <Col>
                        <Card className="card-v2">
                            <CardBody>
                                <Row>
                                    <Col xs="9" className="pr-0">
                                        <CardText>Time: <strong>{x.solderingResult.tenthsOfSecondSoldering / 10} seconds</strong></CardText>
                                        <CardText>Temperature: <strong>{x.selectedTemperature / 9} C</strong></CardText>
                                    </Col>
                                    <Col xs="3" className="text-right">
                                        <a className="pointer" onClick={() => this.props.handleDeleteRealTimeData(x.date)}>
                                            <FontAwesomeIcon icon={faTrashAlt} />
                                        </a>
                                    </Col>
                                </Row>
                                <Row>
                                    <Plot2AxisTiny
                                        plotData={data}
                                        counterTenthSecond={x.data.length}
                                        interval={Math.floor(x.data.length / 2)}
                                    />
                                </Row>
                            </CardBody>
                        </Card>
                    </Col>);
            });

            return (
                <Row xs={1} sm={2} md={5}>
                    {registeredPlots}
                </Row>
            )
        }

        const getRegisteredPlotsCard = () => {
            return (
                <Card>
                    <CardBody>
                        {getRegisteredPlots()}
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row className="mb-3">
                    {getPortStatus()}
                </Row>
                <Row className="mb-3">
                    {getConfigurationCard()}
                </Row>
                <Row>
                    {getRegisteredPlotsCard()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_TempTest;
