import React from 'react'
import CardLinkSectionView from '../../CardLinkSection';

const MaintenanceCardView = () => {
    return (
        <CardLinkSectionView
            to="/maintenance"
            imgPath="img/iconImg/manteinance.png"
            cardTitle="Maintenance"
            alt="Maintenance"
        />
    )
}

export default MaintenanceCardView;