import React from 'react'
import CardLinkApp from '../../CardLinkApp';

const ProcessOptimizationCardView = () => {
    return (
        <CardLinkApp 
            to="/processes/optimization"
            imgPath="img/iconImg/processOptimization.png"
            cardText="Optimize your processes by choosing the best combination of working temperature and the ideal cartridge."
            cardTitle="Process optimization"
            alt="Process Optimization"
        />
    )
}

export default ProcessOptimizationCardView;

