import * as React from 'react';
import { Container } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import ProcessOptimizationCard from '../../content-parts/CardLinkApp/ProcessOptimizationCard';
import SolderingReferenceCard from '../../content-parts/CardLinkApp/SolderingReferenceCard';
import TasksCard from '../../content-parts/CardLinkApp/TasksCard'
import WorkProcessCard from '../../content-parts/CardLinkApp/WorkProcessCard';
import StationsUpdateCard from '../../content-parts/CardLinkApp/StationsUpdateCard';
import QSTCardSection from '../../content-parts/CardLinkSection/QSTCard'
import ProcessesCardSection from '../../content-parts/CardLinkSection/ProcessesCard'
import MaintenanceCard from '../../content-parts/CardLinkSection/MaintenanceCard'

const Home = () => {
    return (
        <React.Fragment>
            <div className="bg-dark-v2 pt-4 pb-4 mb-4">
                <Container>
                    <Row className="mb-1">
                        <h2 className="text-white text-center w-100">Sections</h2>
                    </Row>
                    <Row xs={1} sm={2} md={3} className="mb-5">
                        <Col>
                            <ProcessesCardSection />
                        </Col>
                        <Col>
                            <QSTCardSection />
                        </Col>
                        <Col>
                            <MaintenanceCard />
                        </Col>
                    </Row>
                </Container>
            </div>
            <div>
                <Container>
                    <Row className="mb-1">
                        <h2 className="text-center w-100">Featured applications</h2>
                    </Row>
                    <Row className="content-center" xs={1} sm={2} md={3}>
                        <Col>
                            <ProcessOptimizationCard />
                        </Col>
                        <Col>
                            <SolderingReferenceCard />
                        </Col>
                        <Col>
                            <TasksCard />
                        </Col>
                        <Col>
                            <WorkProcessCard/>
                        </Col>
                        <Col>
                            <StationsUpdateCard />
                        </Col>
                    </Row>
                </Container>
            </div>
        </React.Fragment>
    )
};

export default Home;
