export function TryParseInt(str, defaultValue) {
    var retValue = defaultValue;
    if (str !== null) {
        if (str.length > 0) {
            if (!isNaN(str)) {
                retValue = parseInt(str);
            }
        }
    }
    return retValue;
}

export function Round(num, decimals = 2) {
    var f = 1;
    for (var i = 0; i < decimals; i++) {
        f *= 10;
    }

    return Math.round((num + Number.EPSILON) * f) / f;
}
