import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch, AnyAction } from 'redux'
import { ApplicationState } from '../../../../store';
import * as StationStore from '../../../../store/Station';
import * as StationUpdateStore from '../../../../store/StationUpdate';
import StationService from '../../../../services/station.service';
import StationsUpdateService from '../../../../services/stationsUpdate.service';
import { IUpdateStation } from './data/IUpdateStation';
import StationsUpdateView from './StationsUpdateView';

type StationsUpdateProps = {
    stationControllers: StationStore.StationController[],
    stationsUpdate: StationUpdateStore.StationUpdate[],
    stationActions: typeof StationStore.actionCreators,
    stationUpdateActions: typeof StationUpdateStore.actionCreators
} & StationStore.StationState
  & StationUpdateStore.StationUpdateState
  & typeof StationStore.actionCreators
  & typeof StationUpdateStore.actionCreators;

interface IState {
    isLoadingStations: boolean,
    isLoadingUpdates: boolean
}

class StationsUpdate extends React.PureComponent<StationsUpdateProps, IState> {
    constructor(props: StationsUpdateProps) {
        super(props);

        this.state = {
            isLoadingStations: true,
            isLoadingUpdates: true
        };

        this.searchStations(this.props);
        this.searchStationsUpdates(this.props);
    }

    searchStations(props: StationsUpdateProps) {
        props.stationActions.deleteAllStationControllers();
        this.setState({
            isLoadingStations: true
        })

        StationService
            .getStations()
            .then(scs => {
                if (Array.isArray(scs)) {
                    scs.forEach((sc: StationStore.StationController) => {
                        props.stationActions.createStationController(sc);

                        sc.stations.forEach((s: StationStore.Station) =>
                            props.stationActions.createStation(s, sc.id)
                        )
                    })
                }

                this.setState({
                    isLoadingStations: false
                })
            });
    }

    searchStationsUpdates(props: StationsUpdateProps) {
        props.stationUpdateActions.deleteAllStationUpdates();

        this.setState({
            isLoadingUpdates: true
        })

        StationsUpdateService
            .getStationsUpdateInfo()
            .then(updates => {
                if (Array.isArray(updates)) {
                    updates.forEach((x: StationUpdateStore.StationUpdate) =>
                        props.stationUpdateActions.createStationUpdate(x)
                    )
                }

                this.setState({
                    isLoadingUpdates: false
                })
            });
    }

    public render() {
        const handleUpdateClick = (stationsToUpdate: IUpdateStation[]) => {
            StationsUpdateService.updateStations(stationsToUpdate);
        }

        return (
            <StationsUpdateView
                stationControllers={this.props.stationControllers}
                stationsUpdate={this.props.stationsUpdate}
                isLoading={this.state.isLoadingStations && this.state.isLoadingUpdates}
                handleUpdateClick={handleUpdateClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers,
        stationsUpdate: state.stationUpdate.stationsUpdate
    };
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction>) => {
    return {
        stationActions: bindActionCreators(StationStore.actionCreators, dispatch),
        stationUpdateActions: bindActionCreators(StationUpdateStore.actionCreators, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(StationsUpdate);
