import * as React from 'react';
import { Container } from 'reactstrap';
import * as StationStore from '../../../../store/Station';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';
import SolderingReferenceNewStep1 from './SolderingReferenceNewStep1';
import SolderingReferenceNewStep2 from './SolderingReferenceNewStep2';

type SolderingReferenceProps = {
    currentStep: number,
    isLoading: boolean,
    isFormCorrect: boolean,

    stationControllers: StationStore.StationController[],

    lastRealTimeData: IRealTimeData,
    detectedSoldering: ISolderingData,

    selectedTool: number,
    selectedTemperature: number,
    selectedCartridge: string,
    selectedName: string,

    handleStationClick: (stationControllerId: string, station: StationStore.Station, port: number, tool: number) => void,
    handleSelecTempChange: (temperature: string) => void,
    handleCartridgeChange: (value: string) => void,
    handleNameChange: (value: string) => void,
    handleNextClick: () => void,
    handleSaveReferenceClick: () => void
}

class SolderingReferenceNewView extends React.PureComponent<SolderingReferenceProps> {
    public render() {
        return (
            <Container>
                <SolderingReferenceNewStep1
                    currentStep={this.props.currentStep}
                    isLoading={this.props.isLoading}
                    isFormCorrect={this.props.isFormCorrect}

                    stationControllers={this.props.stationControllers}

                    selectedTool={this.props.selectedTool}
                    selectedTemperature={this.props.selectedTemperature}

                    handleStationClick={this.props.handleStationClick}
                    handleSelecTempChange={this.props.handleSelecTempChange}
                    handleCartridgeChange={this.props.handleCartridgeChange}
                    handleNameChange={this.props.handleNameChange}
                    handleNextClick={this.props.handleNextClick}
                />
                <SolderingReferenceNewStep2
                    currentStep={this.props.currentStep}
                    lastRealTimeData={this.props.lastRealTimeData}
                    detectedSoldering={this.props.detectedSoldering}

                    selectedTemperature={this.props.selectedTemperature}
                    selectedCartridge={this.props.selectedCartridge}
                    selectedName={this.props.selectedName}

                    handleNextClick={this.props.handleNextClick}
                    handleSaveReferenceClick={this.props.handleSaveReferenceClick}
                />
            </Container>
        );
    }
};

export default SolderingReferenceNewView;
