import Home from './components/content-pages/Home';
import Maintenance from './components/content-pages/maintenance/Maintenance';
import StationsUpdate from './components/content-pages/maintenance/StationsUpdate';
import Processes from './components/content-pages/processes/Processes';
import ProcessOptimization from './components/content-pages/processes/ProcessOptimization';
import SolderingsReference from './components/content-pages/processes/SolderingsReference';
import SolderingReferenceNew from './components/content-pages/processes/SolderingReferenceNew';
import Traceability from './components/content-pages/traceability/Traceability';
import Tasks from './components/content-pages/traceability/Tasks';
import TaskNew from './components/content-pages/traceability/TaskNew';
import TaskEdit from './components/content-pages/traceability/TaskEdit';
import TaskDetail from './components/content-pages/traceability/TaskDetail';
import TaskHistoryDetail from './components/content-pages/traceability/TaskHistoryDetail';
import WorkProcess from './components/content-pages/traceability/WorkProcess';
import StationsManager from './components/content-pages/traceability/StationsManager';

export default [
  { path: "/", name: "Home", Component: Home },
  { path: "/maintenance", name: "Maintenance", Component: Maintenance },
  { path: "/maintenance/stationsupdate", name: "Stations update", Component: StationsUpdate },
  { path: "/processes", name: "Processes", Component: Processes },
  { path: "/processes/optimization", name: "Process optimization", Component: ProcessOptimization },
  { path: "/processes/solderingreference", name: "Solderings reference", Component: SolderingsReference },
  { path: "/processes/solderingreference/new", name: "New", Component: SolderingReferenceNew },
  { path: "/qst", name: "QST", Component: Traceability },
  { path: "/qst/task", name: "Tasks", Component: Tasks },
  { path: "/qst/task/new", name: "New", Component: TaskNew },
  { path: "/qst/task/:id/detail", name: "Details", Component: TaskDetail },
  { path: "/qst/task/:id/edit", name: "Edit", Component: TaskEdit },
  { path: "/qst/task/:taskId/history/:historyId", name: "History", Component: TaskHistoryDetail },
  { path: "/qst/workprocess", name: "Work process", Component: WorkProcess },
  { path: "/qst/stationsmanager", name: "Stations manager", Component: StationsManager }
];
