import * as React from 'react';
import { Container } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import TasksCard from '../../../content-parts/CardLinkApp/TasksCard';
import WorkProcessCard from '../../../content-parts/CardLinkApp/WorkProcessCard';
import StationManagerCard from '../../../content-parts/CardLinkApp/StationManagerCard';

const Traceability = () =>{
        return (
            <Container>
                <Row className="content-center" xs={1} sm={2} md={3}>
                    <Col>
                        <TasksCard/>
                    </Col>
                    <Col>
                        <WorkProcessCard/>
                    </Col>
                    <Col>
                        <StationManagerCard/>
                    </Col>
                </Row>
            </Container>
        );
};

export default Traceability;
