import * as React from 'react';
import { connect } from 'react-redux';
import { ApplicationState } from '../../../../store';
import * as AssistantStore from '../../../../store/Assistant';
import AssistantService from '../../../../services/assistant.service';
import SolderingsReferenceView from './SolderingsReferenceView';

type SolderingsReferenceProps =
    AssistantStore.AssistantState &
    typeof AssistantStore.actionCreators;

interface IState {
    isLoading: boolean,
    nTotalReferences: number,
    pageSize: number,
    pageNumber: number
}

class SolderingsReference extends React.PureComponent<SolderingsReferenceProps, IState> {
    constructor(props: SolderingsReferenceProps) {
        super(props);

        this.state = {
            isLoading: true,
            nTotalReferences: 0,
            pageSize: 25,
            pageNumber: 1
        };

        this.searchReferences(this.props, this.state.pageNumber);
    }

    searchReferences(props: SolderingsReferenceProps, pageNumber: number) {
        props.deleteAllReferences();

        this.setState({
            isLoading: true,
            pageNumber: pageNumber
        })

        AssistantService
            .getReferences(pageNumber, this.state.pageSize)
            .then(references => {
                if (Array.isArray(references)) {
                    references.forEach((x: AssistantStore.Reference) =>
                        props.createReference(x)
                    )
                }

                this.setState({
                    isLoading: false
                })
            });

        AssistantService
            .countReferences()
            .then(value => {
                this.setState({
                    nTotalReferences: value
                })
            });
    }

    public render() {
        const handleDeleteReference = (id: number) => {
            AssistantService
                .deleteReference(id)
                .then(() =>
                    this.searchReferences(
                        this.props,
                        this.state.pageNumber)
                );
        }

        const handleUploadReference = (file: FormData) => {
            AssistantService
                .uploadReference(file)
                .then(() =>
                    this.searchReferences(
                        this.props,
                        this.state.pageNumber)
                );
        }

        const handleDownloadReference = (id: number, name: string) => {
            if (!name) {
                let date = new Date()
                name = date.toISOString();
            }

            AssistantService
                .downloadReference(id)
                .then((value) => {
                    let data = JSON.stringify(value);
                    const url = window.URL.createObjectURL(new Blob([data]));
                    let link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', name.trim() + '.jsr');
                    document.body.appendChild(link);

                    link.click();

                    document.body.removeChild(link);
                });
        }

        const handlePageClick = (pageNumber: number) => {
            this.searchReferences(
                this.props,
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchReferences(
                this.props,
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchReferences(
                this.props,
                Math.min(
                    Math.ceil(this.state.nTotalReferences / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        return (
            <SolderingsReferenceView
                references={this.props.references}
                nTotalReferences={this.state.nTotalReferences}
                pageSize={this.state.pageSize}
                pageNumber={this.state.pageNumber}
                isLoading={this.state.isLoading}

                deleteReference={handleDeleteReference}
                uploadReference={handleUploadReference}
                downloadReference={handleDownloadReference}

                handlePageClick={handlePageClick}
                handlePreviousPageClick={handlePreviousPageClick}
                handleNextPageClick={handleNextPageClick}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        references: state.assistant.references
    };
};

export default connect(
    mapStateToProps,
    AssistantStore.actionCreators
)(SolderingsReference);
