import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import * as AssistantStore from '../../../../store/Assistant';
import * as StationStore from '../../../../store/Station';
import * as TaskStore from '../../../../store/Task';
import { GetPortStatus2String, GetPortStatus2Lights } from '../../../../utils/portUtils';
import { ToolToString } from '../../../../utils/toolUtils';
import { IRealTimeData } from './data/IRealTimeData';
import Plot2Axis from '../../../content-parts/Plot2Axis';
import PlotReference from '../../../content-parts/PlotReference';
import PortStatusCard from '../../../content-parts/PortStatusCard';

type WorkProcessProps = {
    selectedTask: TaskStore.Task,
    selectedStation: StationStore.Station,
    selectedPort: number,
    reference: AssistantStore.Reference,

    queueId: number,

    plotData: IRealTimeData[],
    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,
    interval: number
}

class WorkProcessStep3 extends React.PureComponent<WorkProcessProps> {
    public render() {
        const getDetails = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>Task name: {this.props.selectedTask.name}</CardText>
                        <CardText>Task code: {this.props.selectedTask.id}</CardText>
                        <hr />
                        <CardText>Station: {this.props.selectedStation.name} [{this.props.selectedStation.model}]</CardText>
                        <CardText>Port: {this.props.selectedPort + 1}</CardText>
                        <hr />
                        <CardText>Tool: {ToolToString(this.props.selectedTask.workConfig.tool)}</CardText>
                        {this.props.selectedTask.workConfig.changeSelectedTemp ?
                            <CardText>
                                Selected temperature: {this.props.selectedTask.workConfig.selectedTemp / 9} C
                            </CardText> :
                            ""
                        }
                        {this.props.selectedTask.workConfig.changeSolderingReference ?
                            <CardText>
                                Soldering reference: {this.props.reference.name.length === 0 ? "[No name]" : this.props.reference.name}
                            </CardText> :
                            ""
                        }
                    </CardBody>
                </Card>
            )
        };

        const getPlotRecordData = () => {
            let portData = this.props.plotData[this.props.plotData.length - 1];
            let circleLight = "circle-light-" + GetPortStatus2Lights(
                portData.toolStatus,
                portData.isCalibrating,
                portData.isSoldering,
                portData.isPortEnabled);

            return (
                <Card>
                    <CardBody>
                        <Row className="pb-2">
                            <Col xs="auto">
                                Temperature:{' '}
                                <span className="temp-value"> {Math.floor(portData.temperature)} C</span>
                            </Col>
                            <Col>
                                Power:{' '}
                                <span className="power-value">{Math.floor(portData.power)} %</span>
                            </Col>
                            <Col className="text-right">
                                <FontAwesomeIcon icon={faCircle} className={circleLight} />
                                <strong className="pl-1">{GetPortStatus2String(
                                    portData.toolStatus,
                                    portData.isCalibrating,
                                    portData.isSoldering,
                                    portData.isPortEnabled
                                )}</strong>
                            </Col>
                        </Row>
                        <Row>
                            <Plot2Axis
                                plotData={this.props.plotData}
                                counterTenthSecond={this.props.counterTenthSecond}
                                interval={this.props.interval}
                            />
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getPlotReference = () => {
            return (
                <Card>
                    <CardBody>
                        <PlotReference
                            soldering={this.props.plotData.map(x => x.power)}
                            reference={this.props.reference.power.map(x => x / 10)}
                        />
                    </CardBody>
                </Card>
            )
        }

        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />
        }

        const layoutWithReference = () => {
            return (
                <React.Fragment>
                    <Row className="mb-3">
                        {getPortStatus()}
                    </Row>
                    <Row className="mb-3">
                        <Col xs="8" className="pl-0">
                            {getPlotReference()}
                        </Col>
                        <Col xs="4" className="pl-0 pr-0">
                            {getDetails()}
                        </Col>
                    </Row>
                </React.Fragment>)
        }

        const layoutWithOutReference = () => {
            return (
                <React.Fragment>
                    <Row className="mb-3">
                        <Col xs="8" className="pl-0">
                            {getPlotRecordData()}
                        </Col>
                        <Col xs="4" className="pl-0 pr-0">
                            {getDetails()}
                        </Col>
                    </Row>
                </React.Fragment>)
        }

        return (
            this.props.selectedTask.workConfig.changeSolderingReference ?
                layoutWithReference() :
                layoutWithOutReference()
        );
    }
};

export default WorkProcessStep3;
