import React from 'react'
import { Link } from 'react-router-dom';
import { Col, Row } from 'reactstrap';
import { Card, CardBody, CardTitle, CardImg } from 'reactstrap';
import { NavLink } from 'reactstrap';
import './Card.css'

type CardProps = {
    to: string,
    imgPath: string,
    cardTitle: string,
    alt: string
};

const CardLinkSectionView = ({ to, imgPath, cardTitle, alt }: CardProps) => {
    return (
        <NavLink className="card-link-section" tag={Link} to={to}>
            <Card className="box-shadow-v2">
                <CardBody>
                    <Row>
                        <Col xs="2">
                            <CardImg src={imgPath} alt={alt} />
                        </Col>
                        <Col xs="10">
                            <CardTitle>{cardTitle}</CardTitle>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
        </NavLink>
    )
}

export default CardLinkSectionView;