import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class StationsUpdateService {
    static async getStationsUpdateInfo() {
        let stations;

        if (Configuration.FakeMode) {
            stations = FakeService.getStationsUpdateInfo();
        }
        else {
            stations = await RestService.getStationsUpdateInfo();
        }

        return stations;
    }

    static async updateStations(stationsToUpdate) {
        if (Configuration.FakeMode) {
            FakeService.updateStations();
        }
        else {
            await RestService.updateStations(stationsToUpdate);
        }
    }
}

export default StationsUpdateService;
