import * as React from 'react';
import { Container } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import ProcessOptimizationCard from '../../../content-parts/CardLinkApp/ProcessOptimizationCard';
import SolderingReferenceCardView from '../../../content-parts/CardLinkApp/SolderingReferenceCard/SolderingReferenceCardView';

const Processes = () => {
    return(
        <Container>
            <Row className="content-center" xs={1} sm={2} md={3}>
                <Col>
                    <ProcessOptimizationCard/>
                </Col>
                <Col>
                    <SolderingReferenceCardView/>
                </Col>
            </Row>
        </Container>
    )
};

export default Processes;
