import * as React from 'react';
import { Col, Row, Input } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { ResponsiveContainer, XAxis, YAxis, CartesianGrid, Legend, LineChart, Line } from 'recharts';
import { RemoveDuplicates } from '../../../../utils/arrayUtils';
import { Average } from '../../../../utils/mathUtils';
import { Round } from '../../../../utils/numberUtils';
import { GetPlotColors } from '../../../../utils/plotUtils';
import { SecToString } from '../../../../utils/timeUtils';
import { ISolderingData } from './data/ISolderingData';
import { IRealTimeData } from './data/IRealTimeData';

interface ICartridge {
    name: string,
    time: number,
    energy: number,
    data: IRealTimeData[]
}

type ProcessOptimizationProps = {
    isLoading: boolean,
    detectedSolderings: ISolderingData[],
    fasterSoldering: ISolderingData,
    handleCartridgeChange: (value: string) => void
}

class ProcessOptimizationStep_CartridgeResult extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getPlot = (cartridges: ICartridge[]) => {

            let length = Math.max.apply(Math, cartridges.map(function(x) { return x.data.length; }));
            let data: { [key: string]: number }[] = [];

            for (var i = 0; i < length; i++) {
                data.push({});
            }
            
            for (var i = 0; i < cartridges.length; i++) {
                let cartridge = cartridges[i];

                for (var j = 0; j < cartridge.data.length; j++) {
                    let record = data[j];
                    record[cartridge.name] = cartridge.data[j].power;
                    data[j] = record;
                }
            }

            let colors = GetPlotColors();
            let lines = cartridges.map((x, index) => {
                let color = colors[index % colors.length];
                return <Line dataKey={x.name} yAxisId="left" type="monotone" stroke={color["stroke"]} dot={false} />
            });

            return (
                <ResponsiveContainer width='100%' aspect={2.5}>
                    <LineChart
                        data={data}
                        margin={{
                            left: -10,
                            right: -10
                        }}
                    >
                        <CartesianGrid stroke="#e6e6e6" />
                        <Legend />

                        <XAxis
                            dataKey="time"
                            height={50}
                            label={"Time (s)"}
                            tickFormatter={(tick) => SecToString(Math.floor(tick / 10))}
                            interval={Math.floor(data.length / 5)} />
                        <YAxis
                            yAxisId="left"
                            orientation="left"
                            domain={['auto', 'auto']} />

                        {lines}
                    </LineChart>
                </ResponsiveContainer>
            );
        }

        const getPlotCard = (cartridges: ICartridge[]) => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>;
            }
            else {
                cardBody =
                    <React.Fragment>
                        <CardText>Best cartridge: <strong>{this.props.fasterSoldering.cartridge}</strong></CardText>
                        {getPlot(cartridges)}
                    </React.Fragment>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getTableCard = (cartridges: ICartridge[]) => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>;
            }
            else {
                let selected = cartridges.find(x => x.name === this.props.fasterSoldering.cartridge)

                let options =
                    cartridges.map(x => {
                        let delay =
                            selected === undefined ? 0 :
                                Math.round(((x.time / selected.time) - 1) * 100);

                        return (
                            <tr className={this.props.fasterSoldering.cartridge === x.name ? "font-weight-bold" : ""}>
                                <td>
                                    <Input
                                        type="radio"
                                        name="radio1"
                                        defaultChecked={this.props.fasterSoldering.cartridge === x.name}
                                        onChange={(e) => this.props.handleCartridgeChange(x.name)}
                                    />
                                </td>
                                <td>{x.name}</td>
                                <td>{x.time / 10}</td>
                                <td>{this.props.fasterSoldering.cartridge === x.name ? "-" : delay}</td>
                                <td>{x.energy}</td>
                            </tr>)
                    })

                cardBody =
                    <table className="table table-sm">
                        <thead>
                            <tr>
                                <th scope="col"></th>
                                <th scope="col">Cartridge</th>
                                <th scope="col">Time average (s)</th>
                                <th scope="col">Delay (%)</th>
                                <th scope="col">Energy (ws)</th>
                            </tr>
                        </thead>
                        <tbody>
                            {options}
                        </tbody>
                    </table>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getBestCartridges = () => {
            let cartridges = this.props.detectedSolderings.map(x => x.cartridge);
            cartridges = RemoveDuplicates(cartridges);

            let cartridgesStatistics: ICartridge[] =
                cartridges.map(cartridge => {

                    let currentCartridge = this.props.detectedSolderings
                        .filter(x => x.cartridge === cartridge);

                    let times = currentCartridge
                        .map(x => x.solderingResult.tenthsOfSecondSoldering);

                    let energies = currentCartridge
                        .map(x => x.solderingResult.energy);

                    let timeAvg = Math.round(Average(times));
                    let energyAvg = Round(Average(energies), 1);

                    return {
                        name: cartridge,
                        time: timeAvg,
                        energy: energyAvg,
                        data: currentCartridge[0].data
                    }
                })

            cartridgesStatistics.sort(function (a:ICartridge, b:ICartridge) {
                return a.time < b.time ? -1 : 1;
            })
            cartridgesStatistics = cartridgesStatistics.slice(0, 5);

            return cartridgesStatistics;
        }

        let cartridges = getBestCartridges();

        return (
            <React.Fragment>
                <Row>
                    <Col xs="6" className="pl-0 pr-0">
                        {getPlotCard(cartridges)}
                    </Col>
                    <Col xs="6">
                        {getTableCard(cartridges)}
                    </Col>
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_CartridgeResult;
