import React from 'react'
import CardLinkSectionView from '../../CardLinkSection';

const QSTCardView = () => {
    return (
        <CardLinkSectionView
            to="/qst"
            imgPath="img/iconImg/traceability.png"
            cardTitle="QST"
            alt="QST"
        />
    )
}

export default QSTCardView;