import * as React from 'react';
import { connect } from 'react-redux';
import { Redirect } from "react-router-dom";
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import AssistantService from '../../../../services/assistant.service';
import TasksService from '../../../../services/tasks.service';
import TaskNewView from './TaskNewView';

type TaskNewProps = {
}

interface IState {
    redirect: string,
    isLoading: boolean,
    reference: AssistantStore.Reference
}

class TaskNew extends React.PureComponent<TaskNewProps, IState> {
    constructor(props: TaskNewProps) {
        super(props);

        this.state = {
            redirect: "",
            isLoading: false,
            reference: {
                id: -1,
                name: "",
                power: [],
                cartridge: "",
                temperature: 0,
                durationTenthsSeconds: 0
            }
        };
    }

    public render() {
        const handleRefSolderingChange = (id: number) => {
            this.setState({
                isLoading: true
            })

            AssistantService
                .getReferenceById(id)
                .then(reference => {
                    this.setState({
                        isLoading: false,
                        reference: reference
                    })
                });
        }

        const handleNewTask = (task: TaskStore.Task) => {
            TasksService
                .createTask(task)
                .then(x => {
                    this.setState({
                        redirect: "/qst/task"
                    });
                });
        }

        if (this.state.redirect.length > 0) {
            return <Redirect to={this.state.redirect} />
        }

        return (
            <TaskNewView
                isLoading={this.state.isLoading}
                reference={this.state.reference}

                handleRefSolderingChange={handleRefSolderingChange}
                newTask={handleNewTask}
            />
        );
    }
};

export default connect(
    null,
    TaskStore.actionCreators
)(TaskNew);
