import { Action, Reducer } from 'redux';

export interface StationUpdate {
    model: string,
    hardware: string,
    software: string,
    language: string
}

export interface StationUpdateState {
    stationsUpdate: StationUpdate[]
}

export interface CreateStationUpdateAction { type: 'CREATE_STATION_UPDATE', x: StationUpdate }
export interface DeleteStationUpdateAction { type: 'DELETE_STATION_UPDATE', software: string }
export interface DeleteAllStationUpdatesAction { type: 'DELETE_ALL_STATION_UPDATES' }

export type KnownAction =
    | CreateStationUpdateAction
    | DeleteStationUpdateAction
    | DeleteAllStationUpdatesAction;

export const actionCreators = {
    createStationUpdate: (x: StationUpdate) => ({ type: 'CREATE_STATION_UPDATE', x: x } as CreateStationUpdateAction),
    deleteStationUpdate: (software: string) => ({ type: 'DELETE_STATION_UPDATE', software: software } as DeleteStationUpdateAction),
    deleteAllStationUpdates: () => ({ type: 'DELETE_ALL_STATION_UPDATES' } as DeleteAllStationUpdatesAction)
};


export const reducer: Reducer<StationUpdateState> = (
    state: StationUpdateState | undefined,
    incomingAction: Action
): StationUpdateState => {
    if (state === undefined) {
        return { stationsUpdate: [] };
    }

    const action = incomingAction as KnownAction;
    switch (action.type) {
        case 'CREATE_STATION_UPDATE':
            if (state.stationsUpdate.find(x => x.software === action.x.software)
            ) {
                return {
                    ...state
                }
            }

            return {
                ...state,
                stationsUpdate: state.stationsUpdate.concat(action.x)
            };

        case 'DELETE_STATION_UPDATE':
            return {
                ...state,
                stationsUpdate: state.stationsUpdate
                    .filter(x => {
                        if (x.software === action.software) {
                            return x;
                        }
                    })
            };

        case 'DELETE_ALL_STATION_UPDATES':
            return {
                ...state,
                stationsUpdate: []
            };

        default:
            return state;
    }
};
