import * as React from 'react';
import { Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Input } from 'reactstrap';

type ProcessOptimizationProps = {
    selectedTemperature: number,
    handleSelecTempChange: (temperature: string) => void
}

class ProcessOptimizationStep_Temp extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            <strong>Selected temperature:</strong>
                            <Input
                                type="select"
                                defaultValue={this.props.selectedTemperature / 9}
                                onChange={(e) => this.props.handleSelecTempChange(e.target.value)}
                            >
                                <option>425</option>
                                <option>400</option>
                                <option>375</option>
                                <option>350</option>
                                <option>325</option>
                                <option>300</option>
                            </Input>
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row>
                    {getConfigurationCard()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_Temp;
