import axios from 'axios';
const proxy = process.env.REACT_APP_PROXY;


const ENDPOINT_ASSISTANT =            '/api/assistant'
const ENDPOINT_PROCESS_OPTIMIZATION = '/api/processoptimization'
const ENDPOINT_REAL_TIME =            '/api/realtimedata';
const ENDPOINT_STATIONS =             '/api/station';
const ENDPOINT_STATIONS_UPDATE =      '/api/stationupdate';
const ENDPOINT_TRACE =                '/api/trace';
const ENDPOINT_WORK =                 '/api/work';
const ENDPOINT_WORK_HISTORY =         '/api/workhistory';

class RestService {

    // assistant

    static async getSolderingResult(stationControllerId, stationId, port) {
        return await this.doGetRequest(
            ENDPOINT_ASSISTANT +
            "/solderingresult" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port);
    }

    static async countReferences() {
        return await this.doGetRequest(ENDPOINT_ASSISTANT + "/count");
    }

    static async getReferences(pageNumber, pageSize) {
        return await this.doGetRequest(ENDPOINT_ASSISTANT + "?pagenumber=" + pageNumber + "&pagesize=" + pageSize);
    }

    static async getReferenceById(id) {
        return await this.doGetRequest(ENDPOINT_ASSISTANT + "/" + id);
    }

    static async deleteReference(id) {
        return await this.doDeleteRequest(ENDPOINT_ASSISTANT + "/" + id);
    }

    static async uploadReference(file) {
        return await this.doFormDataPostRequest(ENDPOINT_ASSISTANT + "/uploadreference", file);
    }

    static async downloadReference(id) {
        return await this.doGetRequest(ENDPOINT_ASSISTANT + "/downloadreference?id=" + id);
    }

    static async generateReferenceSoldering(stationControllerId, stationId, port, name, cartridge) {
        return await this.doPostRequest(
            ENDPOINT_ASSISTANT +
            "/generatereferencesoldering" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port +
            "&name=" + name +
            "&cartridge=" + cartridge);
    }

    // tasks

    static async countTasks(filter) {
        return await this.doGetRequest(ENDPOINT_WORK + "/count?filter=" + filter);
    }

    static async getTasks(filter, pageNumber, pageSize) {
        return await this.doGetRequest(ENDPOINT_WORK + "?filter=" + filter + "&pagenumber=" + pageNumber + "&pagesize=" + pageSize);
    }

    static async getTaskById(id) {
        return await this.doGetRequest(ENDPOINT_WORK + "/" + id);
    }

    static async createTask(task) {
        return await this.doPostRequest(ENDPOINT_WORK, task);
    }

    static async updateTask(task) {
        return await this.doPutRequest(ENDPOINT_WORK, task);
    }

    static async deleteTask(id) {
        return await this.doDeleteRequest(ENDPOINT_WORK + "/" + id);
    }

    // task histories

    static async countTaskHistories(taskId) {
        return await this.doGetRequest(ENDPOINT_WORK_HISTORY + "/count?workid=" + taskId);
    }

    static async getTaskHistories(taskId, pageNumber, pageSize) {
        return await this.doGetRequest(ENDPOINT_WORK_HISTORY + "?workid=" + taskId + "&pagenumber=" + pageNumber + "&pagesize=" + pageSize);
    }

    static async getTaskHistoryById(id) {
        return await this.doGetRequest(ENDPOINT_WORK_HISTORY + "/" + id);
    }

    // work

    static async startTrace(taskId, stationControllerId, stationId, port) {
        return await this.doPostRequest(
            ENDPOINT_TRACE +
            "/start" +
            "?workid=" + taskId +
            "&stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port);
    }

    static async stopTrace(stationControllerId, stationId, port) {
        return await this.doPostRequest(
            ENDPOINT_TRACE +
            "/stop" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port);
    }

    // real time data

    static async startRealTimeData(stationControllerId, stationId, port) {
        return await this.doPostRequest(
            ENDPOINT_REAL_TIME +
            "/start" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port);
    }

    static async stopRealTimeData(stationControllerId, stationId, queueId) {
        return await this.doPostRequest(
            ENDPOINT_REAL_TIME +
            "/stop" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&queueid=" + queueId);
    }

    static async getRealTimeData(stationControllerId, stationId, queueId) {
        return await this.doGetRequest(
            ENDPOINT_REAL_TIME +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&queueid=" + queueId);
    }

    // process optimization

    static async getBetterOptimizationByCartridge(solderings) {
        return await this.doPostRequest(ENDPOINT_PROCESS_OPTIMIZATION + "/cartridge", solderings);
    }

    static async getBetterOptimizationByTemperature(solderings) {
        return await this.doPostRequest(ENDPOINT_PROCESS_OPTIMIZATION + "/temperature", solderings);
    }

    // stations

    static async getStations() {
        return await this.doGetRequest(ENDPOINT_STATIONS);
    }

    static async setSelectedTemperature(stationControllerId, stationId, port, temperature) {
        return await this.doPostRequest(
            ENDPOINT_STATIONS +
            "/setselectedtemperature" +
            "?stationcontrollerid=" + stationControllerId +
            "&stationid=" + stationId +
            "&stationport=" + port +
            "&temperature=" + temperature);
    }

    static async setTraceabilityMode(stationControllerId, newState) {
        return await this.doPostRequest(
            ENDPOINT_STATIONS +
            "/settraceabilitymode" +
            "?stationcontrollerid=" + stationControllerId +
            "&qststate=" + newState);
    }

    // stations update

    static async getStationsUpdateInfo() {
        return await this.doGetRequest(ENDPOINT_STATIONS_UPDATE);
    }

    static async updateStations(stationsToUpdate) {
        return await this.doPostRequest(ENDPOINT_STATIONS_UPDATE, stationsToUpdate);
    }

    // api requests

    static async doGetRequest(resource) {
        try {
            const success = await axios({
                method: 'get',
                url: resource,
                baseURL: proxy
            });
            return success["data"];
        } catch (error) {
            console.error("error", error);
            return error;
        }
    }

    static async doPostRequest(resource, data) {
        try {
            const success = await axios({
                method: 'post',
                url: resource,
                baseURL: proxy,
                data: data
            });
            return success["data"];
        } catch (error) {
            console.error("error", error);
            return error;
        }
    }

    static async doFormDataPostRequest(resource, data) {
        try {
            const success = await axios({
                method: 'post',
                url: resource,
                baseURL: proxy,
                data: data,
                config: {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }
            });
            return success["data"];
        } catch (error) {
            return error;
        }
    }

    static async doPutRequest(resource, data) {
        try {
            await axios({
                method: 'put',
                url: resource,
                baseURL: proxy,
                data: data
            });
        } catch (error) {
            console.error("error", error);
        }
    }

    static async doDeleteRequest(resource) {
        try {
            await axios({
                method: 'delete',
                url: resource,
                baseURL: proxy
            });
        } catch (error) {
            console.error("error", error);
        }
    }
}

export default RestService;
