import * as React from 'react';
import { ResponsiveContainer, LineChart, Line, XAxis, YAxis } from 'recharts';

type PlotProps = {
    soldering: number[],
    reference: number[]
}

class PlotReferenceTiny extends React.PureComponent<PlotProps> {
    public render() {
        let plotDataObj = [];

        for (var i: number = 0; i < Math.max(this.props.soldering.length, this.props.reference.length); i++) {
            plotDataObj.push({
                tick: i,
                soldering: i < this.props.soldering.length ? this.props.soldering[i] / 10 : null,
                reference: i < this.props.reference.length ? this.props.reference[i] / 10 : null
            });
        }

        return (
            <ResponsiveContainer width='100%' aspect={2.5}>
                <LineChart
                    data={plotDataObj}
                    margin={{
                        left: -61,
                        right: 0
                    }}
                >
                    <XAxis
                        dataKey="tick"
                        height={0} />
                    <YAxis />

                    <Line dataKey="soldering" type="monotone" dot={false} />
                    <Line dataKey="reference" type="monotone" dot={false} stroke="#bd8531" />
                </LineChart>
            </ResponsiveContainer>
        );
    }
};

export default PlotReferenceTiny;
