import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class AssistantService {
    static async getSolderingResult(stationControllerId, stationId, port) {
        let result;

        if (Configuration.FakeMode) {
            result = FakeService.getSolderingResult();
        }
        else {
            result = await RestService.getSolderingResult(stationControllerId, stationId, port);
        }

        return result;
    }

    static async countReferences() {
        let count;

        if (Configuration.FakeMode) {
            count = FakeService.countReferences();
        }
        else {
            count = await RestService.countReferences();
        }

        return count;
    }

    static async getReferences(pageNumber, pageSize) {
        let references;

        if (Configuration.FakeMode) {
            references = FakeService.getReferences();
        }
        else {
            references = await RestService.getReferences(pageNumber, pageSize);
        }

        return references;
    }

    static async getReferenceById(id) {
        let result;

        if (Configuration.FakeMode) {
            result = FakeService.getReferenceById();
        }
        else {
            result = await RestService.getReferenceById(id);
        }

        return result;
    }

    static async deleteReference(id) {
        if (Configuration.FakeMode) {
            FakeService.deleteReference();
        }
        else {
            await RestService.deleteReference(id);
        }
    }

    static async uploadReference(file) {
        if (Configuration.FakeMode) {
            FakeService.uploadReference();
        }
        else {
            await RestService.uploadReference(file);
        }
    }

    static async downloadReference(id) {
        let result;

        if (Configuration.FakeMode) {
            result = FakeService.downloadReference();
        }
        else {
            result = await RestService.downloadReference(id);
        }

        return result;
    }

    static async generateReferenceSoldering(stationControllerId, stationId, port, name, cartridge) {
        if (Configuration.FakeMode) {
            FakeService.generateReferenceSoldering();
        }
        else {
            await RestService.generateReferenceSoldering(stationControllerId, stationId, port, name, cartridge);
        }
    }
}

export default AssistantService;
