import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch, AnyAction } from 'redux'
import { RouteComponentProps } from 'react-router';
import { Container } from 'reactstrap';
import { Button } from 'reactstrap';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { Row } from 'reactstrap';
import { ApplicationState } from '../../../../store';
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import * as StationStore from '../../../../store/Station';
import AssistantService from '../../../../services/assistant.service';
import StationService from '../../../../services/station.service';
import TasksService from '../../../../services/tasks.service';
import WorkService from '../../../../services/work.service';
import { IRealTimeData } from './data/IRealTimeData';
import { EmptyPlotSeriesData, PushPlotSeriesData } from '../../../../utils/plotUtils';
import WorkProcessStep1 from './WorkProcessStep1';
import WorkProcessStep2 from './WorkProcessStep2';
import WorkProcessStep3 from './WorkProcessStep3';

const WAIT_INTERVAL = 1000;

type WorkProcessProps = {
    stationActions: typeof StationStore.actionCreators,
    taskActions: typeof TaskStore.actionCreators
} & TaskStore.TaskState
  & StationStore.StationState
  & typeof StationStore.actionCreators
  & typeof TaskStore.actionCreators
  & RouteComponentProps<{}>;

interface IState {
    selectedTask: TaskStore.Task,
    selectedStationControllerId: string,
    selectedStation: StationStore.Station,
    selectedPort: number,
    reference: AssistantStore.Reference,

    queueId: number,
    plotData: IRealTimeData[],
    counterTenthSecond: number,
    readyGetMoreData: boolean,
    wasSoldering: boolean,
    lastRealTimeData: IRealTimeData,

    currentStep: number,
    showFinishTaskModal: boolean,
    timer: number,

    nTotalTasks: number,
    pageNumber: number,
    pageSize: number,
    filter: string,

    isLoadingStations: boolean,
    isLoadingTasks: boolean
}

const MAX_POINTS_PLOT = 400;
const INTERVAL_REFRESH_PLOT_MILLISCONDS = 100;

class WorkProcess extends React.PureComponent<WorkProcessProps, IState> {
    constructor(props: WorkProcessProps) {
        super(props);

        let plotData = EmptyPlotSeriesData(MAX_POINTS_PLOT) as IRealTimeData[];

        this.state = {
            selectedTask: {
                id: 0,
                name: "",
                workConfig: {
                    tool: 0,
                    selectedTemp: 0,
                    changeSelectedTemp: false,
                    solderingReferenceId: -1,
                    changeSolderingReference: false,
                    qualityControl: false
                },
                timesPerformed: 0
            },
            selectedStationControllerId: "",
            selectedStation: {
                id: "",
                name: "",
                software: "",
                hardware: "",
                model: "",
                isQSTMode: false,
                ports: []
            },
            selectedPort: -1,
            reference: {
                id: -1,
                name: "",
                power: [],
                cartridge: "",
                temperature: 0,
                durationTenthsSeconds: 0
            },

            queueId: -1,
            plotData: plotData,
            counterTenthSecond: 0,
            readyGetMoreData: true,
            wasSoldering: false,
            lastRealTimeData: {
                temperature: 0,
                power: 0,
                toolStatus: 0,
                isPortEnabled: true,
                isCalibrating: false,
                isSoldering: false,
                tenthsOfSecondSoldering: 0
            },

            currentStep: 1,
            showFinishTaskModal: false,
            timer: -1,

            nTotalTasks: 0,
            pageNumber: 1,
            pageSize: 25,
            filter: "",

            isLoadingStations: true,
            isLoadingTasks: true
        };

        this.searchStations(this.props);
        this.searchTasks(this.props, this.state.filter, this.state.pageNumber);

        setInterval(() => {
            if (this.state.queueId > -1 &&
                this.state.readyGetMoreData) {

                this.setState({
                    readyGetMoreData: false
                })

                WorkService
                    .getRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.queueId)
                    .then((values: IRealTimeData[]) => {
                        if (Array.isArray(values)) {
                            let plotData = this.state.plotData;
                            let counterTenthSecond = this.state.counterTenthSecond;
                            let wasSoldering = this.state.wasSoldering;
                            let lastRealTimeData = this.state.lastRealTimeData;

                            values.forEach(x => {
                                if (this.state.selectedTask.workConfig.changeSolderingReference) {
                                    // raise flag
                                    if (x.isSoldering && !wasSoldering) {
                                        plotData = [];
                                        wasSoldering = true;
                                    }

                                    // down flag
                                    if (!x.isSoldering && wasSoldering) {
                                        wasSoldering = false;
                                    }

                                    // soldering
                                    if (x.isSoldering) {
                                        PushPlotSeriesData(plotData, x, -1);
                                    }
                                }
                                else {
                                    PushPlotSeriesData(plotData, x, MAX_POINTS_PLOT);
                                }

                                lastRealTimeData = x;
                                counterTenthSecond++;
                            });

                            this.setState({
                                plotData: plotData,
                                readyGetMoreData: true,
                                counterTenthSecond: counterTenthSecond,
                                wasSoldering: wasSoldering,
                                lastRealTimeData: lastRealTimeData
                            })
                        }
                    })
            }
        }, INTERVAL_REFRESH_PLOT_MILLISCONDS);
    }

    searchStations(props: WorkProcessProps) {
        props.stationActions.deleteAllStationControllers();
        this.setState({
            isLoadingStations: true
        })

        StationService
            .getStations()
            .then(scs => {
                if (Array.isArray(scs)) {
                    scs.forEach((sc: StationStore.StationController) => {
                        props.stationActions.createStationController(sc);

                        sc.stations.forEach((s: StationStore.Station) =>
                            props.stationActions.createStation(s, sc.id)
                        )
                    })
                }

                this.setState({
                    isLoadingStations: false
                })
            });
    }

    searchTasks(props: WorkProcessProps, filter: string, pageNumber: number) {
        props.taskActions.deleteAllTasks();

        this.setState({
            isLoadingTasks: true,
            pageNumber: pageNumber,
            filter: filter
        })

        TasksService
            .getTasks(filter, pageNumber, this.state.pageSize)
            .then(tasks => {
                if (Array.isArray(tasks)) {
                    tasks.forEach((x: TaskStore.Task) =>
                        props.taskActions.createTask(x)
                    )
                }

                this.setState({
                    isLoadingTasks: false
                })
            });

        TasksService
            .countTasks(filter)
            .then(value => {
                this.setState({
                    nTotalTasks: value
                })
            });
    }

    public render() {
        const getPreviousButton = () => {
            if (this.state.currentStep === 2) {
                return (
                    <Button
                        outline
                        color="primary"
                        size="sm"
                        className="m-2"
                        onClick={() => previous()}
                    >Previous</Button>
                )
            }
        }

        const getNextButton = () => {
            let text = "";
            let isValid = false;

            if (this.state.currentStep === 2) {
                text = "Start task";
                isValid = this.state.selectedStation.id !== "";
            }
            else if (this.state.currentStep === 3) {
                text = "Stop task";
                isValid = true;
            }

            if (!isValid) {
                return "";
            }

            return (
                <Button
                    color="primary"
                    size="sm"
                    className="m-2"
                    onClick={() => next()}
                >{text}</Button>
            )
        }

        const previous = () => {
            this.setState({
                currentStep: this.state.currentStep - 1
            })
        }

        const next = () => {
            if (this.state.currentStep === 1) {
                this.setState({
                    currentStep: 2
                })
            }
            else if (this.state.currentStep === 2) {
                let plotData = this.state.selectedTask.workConfig.changeSolderingReference ?
                    [] : EmptyPlotSeriesData(MAX_POINTS_PLOT) as IRealTimeData[];

                this.setState({
                    plotData: plotData,
                    counterTenthSecond: 0
                })

                WorkService
                    .startTrace(this.state.selectedTask.id, this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort)
                    .then(() =>
                        this.setState({
                            currentStep: 3
                        })
                    );

                WorkService
                    .startRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort)
                    .then((value) => {
                        this.setState({
                            queueId: value
                        })
                    })

                AssistantService
                    .getReferenceById(this.state.selectedTask.workConfig.solderingReferenceId)
                    .then(value => {
                        this.setState({
                            reference: value
                        })
                    });
            }
            else {
                this.setState({
                    showFinishTaskModal: true
                })
            }
        }

        const handleTaskClick = (task: TaskStore.Task) => {
            this.setState({
                selectedTask: task,
                currentStep: 2
            })
        }

        const handleStationClick = (stationControllerId: string, station: StationStore.Station, port: number) => {
            this.setState({
                selectedStationControllerId: stationControllerId,
                selectedStation: station,
                selectedPort: port
            })
        }

        const handleFinishTaskConfirmClick = () => {
            WorkService
                .stopTrace(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort);

            var queueId = this.state.queueId;

            this.setState({
                queueId: -1,
                currentStep: 2,
                showFinishTaskModal: false
            });

            WorkService
                .stopRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, queueId);
        };

        const handleFinishTaskCancelClick = () => {
            this.setState({
                showFinishTaskModal: false
            });
        };

        const handlePageClick = (pageNumber: number) => {
            this.searchTasks(
                this.props,
                this.state.filter,
                pageNumber);
        }

        const handlePreviousPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.max(1, this.state.pageNumber - 1));
        }

        const handleNextPageClick = () => {
            this.searchTasks(
                this.props,
                this.state.filter,
                Math.min(
                    Math.ceil(this.state.nTotalTasks / this.state.pageSize),
                    this.state.pageNumber + 1));
        }

        const handleSearchFieldChange = (filter: string) => {
            if (this.state.timer > 0)
                window.clearTimeout(this.state.timer);

            this.setState({
                pageNumber: 1,
                timer: setTimeout(this.searchTasks, WAIT_INTERVAL, this.props, filter, 1)
            });
        }

        return (
            <Container>
                <Row className="mb-2">
                    <h1>Work process</h1>
                </Row>

                {this.state.currentStep === 1 ?
                    <WorkProcessStep1
                        isLoading={this.state.isLoadingTasks}
                        tasks={this.props.tasks}
                        nTotalTasks={this.state.nTotalTasks}
                        pageSize={this.state.pageSize}
                        pageNumber={this.state.pageNumber}

                        handlePageClick={handlePageClick}
                        handlePreviousPageClick={handlePreviousPageClick}
                        handleNextPageClick={handleNextPageClick}
                        handleTaskClick={handleTaskClick}
                        handleSearchFieldChange={handleSearchFieldChange}
                    /> : ""
                }

                {this.state.currentStep === 2 ?
                    <WorkProcessStep2
                        isLoading={this.state.isLoadingStations}
                        stationControllers={this.props.stationControllers}
                        taskTool={this.state.selectedTask.workConfig.tool}
                        handleStationClick={handleStationClick}
                    /> : ""
                }

                {this.state.currentStep === 3 ?
                    <WorkProcessStep3
                        selectedTask={this.state.selectedTask}
                        selectedStation={this.state.selectedStation}
                        selectedPort={this.state.selectedPort}
                        reference={this.state.reference}

                        queueId={this.state.queueId}
                        plotData={this.state.plotData}
                        lastRealTimeData={this.state.lastRealTimeData}
                        counterTenthSecond={this.state.counterTenthSecond}
                        interval={MAX_POINTS_PLOT / 5}
                    /> : ""
                }

                <Row>
                    {getPreviousButton()}
                    {getNextButton()}
                </Row>

                <Modal isOpen={this.state.showFinishTaskModal} toggle={() => handleFinishTaskCancelClick()}>
                    <ModalHeader toggle={() => handleFinishTaskCancelClick()}>Finish task</ModalHeader>
                    <ModalBody>Are you sure to finish the task?</ModalBody>
                    <ModalFooter>
                        <Button outline color="primary" onClick={() => handleFinishTaskConfirmClick()}>Confirm</Button>{' '}
                        <Button color="primary" onClick={() => handleFinishTaskCancelClick()}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </Container>
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers,
        tasks: state.task.tasks
    };
};

const mapDispatchToProps = (dispatch: Dispatch<AnyAction>) => {
    return {
        stationActions: bindActionCreators(StationStore.actionCreators, dispatch),
        taskActions: bindActionCreators(TaskStore.actionCreators, dispatch)
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(WorkProcess);
