import * as React from 'react';
import { Container } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Row, Button } from 'reactstrap';
import { Modal, ModalHeader, ModalBody, ModalFooter, NavLink } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFile, faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import { faDownload, faUpload } from '@fortawesome/free-solid-svg-icons'
import * as AssistantStore from '../../../../store/Assistant';
import PaginationNav from '../../../content-components/PaginationNav';
import PlotPowerAxisTiny from '../../../content-parts/PlotPowerAxisTiny';

type SolderingsReferenceProps = {
    references: AssistantStore.Reference[],
    nTotalReferences: number,
    pageSize: number,
    pageNumber: number,
    isLoading: boolean,

    deleteReference: (id: number) => void,
    uploadReference: (file: FormData) => void,
    downloadReference: (id: number, name: string) => void,

    handlePageClick: (pageNumber: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void
}

interface IState {
    showDeleteModal: boolean;
    referenceIdToDelete: number;
}

class SolderingsReferenceView extends React.PureComponent<SolderingsReferenceProps, IState> {
    constructor(props: SolderingsReferenceProps) {
        super(props);

        this.state = {
            showDeleteModal: false,
            referenceIdToDelete: -1
        };
    }

    public render() {
        const getListReferences = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            if (this.props.nTotalReferences === 0) {
                return (
                    <React.Fragment>
                        <span>No solderings reference, </span>
                        <NavLink tag={Link} className="p-0 d-inline" to="/processes/solderingreference/new">create new</NavLink>
                        <span> or </span>
                        <NavLink tag={Link} className="p-0 d-inline" onClick={handleUploadClick}>upload one</NavLink>
                        <span>.</span>
                    </React.Fragment>);
            }

            const options = [];

            for (const value of this.props.references) {
                options.push(
                    <tr>
                        <td>{value.id}</td>
                        <td>
                            <PlotPowerAxisTiny
                                plotData={value.power}
                            />
                        </td>
                        <td>{value.name.length === 0 ? "[No name]" : value.name}</td>
                        <td>{value.cartridge}</td>
                        <td>{value.temperature / 9}</td>
                        <td>{value.durationTenthsSeconds / 10}</td>
                        <td>
                            <NavLink tag={Link} className="p-0 pr-3 d-inline" onClick={() => this.props.downloadReference(value.id, value.name)}>
                                <FontAwesomeIcon icon={faDownload} className="fa" />
                            </NavLink>
                            <a href="#" className="pr-3" onClick={(event) => handleDeleteClick(event, value.id)}>
                                <FontAwesomeIcon icon={faTrashAlt} className="fa" />
                            </a>
                        </td>
                    </tr>)
            }

            return (
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Code</th>
                            <th scope="col" className="w-20">Reference</th>
                            <th scope="col">Name</th>
                            <th scope="col">Cartridge</th>
                            <th scope="col">Temperature (C)</th>
                            <th scope="col">Duration (s)</th>
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        }

        const getListReferencesCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardText>
                            {getListReferences()}
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const handleDeleteClick = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>, id: number) => {
            event.preventDefault();

            this.setState({
                referenceIdToDelete: id,
                showDeleteModal: true
            });
        };

        const handleDeleteConfirmClick = () => {
            this.props.deleteReference(this.state.referenceIdToDelete);
            this.setState({
                showDeleteModal: false
            });
        };

        const handleDeleteCancelClick = () => {
            this.setState({
                showDeleteModal: false
            });
        };

        const handleUploadClick = () => {
            fileSelector.click();
        }

        const handleUploadChange = (e: Event) => {
            let input = e.target as HTMLInputElement;

            if (input.files) {
                let file = input.files[0];
                let formData = new FormData();
                formData.append('file', file);

                this.props.uploadReference(formData);
            }
        }

        const fileSelector = document.createElement('input');
        fileSelector.setAttribute('type', 'file');
        fileSelector.setAttribute('accept', '.jsr');
        fileSelector.addEventListener('change', handleUploadChange);

        return (
            <Container>
                <Row>
                    <h1>List of Solderings Reference</h1>
                </Row>
                <Row className="mb-1">
                    <Button
                        outline
                        color="primary"
                        size="sm"
                        className="m-2">
                        <NavLink tag={Link} className="p-0" to="/processes/solderingreference/new">
                            <FontAwesomeIcon icon={faFile} /> Create new
                        </NavLink>
                    </Button>
                    <Button
                        outline
                        color="primary"
                        size="sm"
                        className="m-2"
                        onClick={handleUploadClick}
                    >
                        <FontAwesomeIcon icon={faUpload} /> Upload
                    </Button>
                </Row>
                <Row className="mb-3">
                    {getListReferencesCard()}
                </Row>
                <Row>
                    <PaginationNav
                        totalElements={this.props.nTotalReferences}
                        pageSize={this.props.pageSize}
                        pageNumber={this.props.pageNumber}
                        handlePageClick={this.props.handlePageClick}
                        handlePreviousPageClick={this.props.handlePreviousPageClick}
                        handleNextPageClick={this.props.handleNextPageClick}
                    />
                </Row>

                <Modal isOpen={this.state.showDeleteModal} toggle={() => handleDeleteCancelClick()}>
                    <ModalHeader toggle={() => handleDeleteCancelClick()}>Delete reference</ModalHeader>
                    <ModalBody>Are you sure to delete the reference? This action can not be undone.</ModalBody>
                    <ModalFooter>
                        <Button outline color="primary" onClick={() => handleDeleteConfirmClick()}>Confirm</Button>{' '}
                        <Button color="primary" onClick={() => handleDeleteCancelClick()}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </Container>
        );
    }
};

export default SolderingsReferenceView;
