import Configuration from '../configuration';
import FakeService from './fake.service';
import RestService from './restService';

class WorkService {
    static async startTrace(taskId, stationControllerId, stationId, port) {
        if (Configuration.FakeMode) {
            FakeService.startTrace();
        }
        else {
            await RestService.startTrace(taskId, stationControllerId, stationId, port);
        }
    }

    static async stopTrace(stationControllerId, stationId, port) {
        if (Configuration.FakeMode) {
            FakeService.stopTrace();
        }
        else {
            await RestService.stopTrace(stationControllerId, stationId, port);
        }
    }

    static async startRealTimeData(stationControllerId, stationId, port) {
        let queueId;

        if (Configuration.FakeMode) {
            queueId = FakeService.startRealTimeData();
        }
        else {
            queueId = await RestService.startRealTimeData(stationControllerId, stationId, port);
        }

        return queueId;
    }

    static async stopRealTimeData(stationControllerId, stationId, queueId) {
        if (Configuration.FakeMode) {
            FakeService.stopRealTimeData();
        }
        else {
            await RestService.stopRealTimeData(stationControllerId, stationId, queueId);
        }
    }

    static async getRealTimeData(stationControllerId, stationId, queueId) {
        let data;

        if (Configuration.FakeMode) {
            data = FakeService.getRealTimeData();
        }
        else {
            data = await RestService.getRealTimeData(stationControllerId, stationId, queueId);
        }

        return data;
    }
}

export default WorkService;
