import React from 'react'
import CardLinkApp from '../../CardLinkApp';


const SolderingReferenceCardView = () => {
    return (
        <CardLinkApp 
            to="/processes/solderingreference"
            imgPath="img/iconImg/solderingAssistant.png"
            cardText="Pre-stored soldering joint and compare in real-time with all the new soldering processes."
            cardTitle="Solderings reference"
            alt="Solderings Reference"
        />
    )
}

export default SolderingReferenceCardView;

