import React from 'react'
import CardLinkApp from '../../CardLinkApp';

const WorkProcessCardView = () => {
    return (
        <CardLinkApp 
            to="/qst/workprocess"
            imgPath="img/iconImg/workProcess.png"
            cardText="Register your work in the QST system."
            cardTitle="Work process"
            alt="Work Process"
        />
    )
}

export default WorkProcessCardView;

