import * as React from 'react';
import './MultiStepNav.scss';

type MultiStepNavProps = {
    labels: string[],
    current: number
}

class MultiStepNav extends React.PureComponent<MultiStepNavProps> {
    public render() {
        let items = this.props.labels.map((x, index) =>
            <li className={index === this.props.current ? "active" : ""}>
                <span>{x}</span>
            </li>
        )

        return (
            <React.Fragment>
                <ul className="multi-step-nav">{items}</ul>
            </React.Fragment>
        );
    }
};

export default MultiStepNav;
