import * as React from 'react';
import { Container } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Col, Row, Button, CustomInput, Input } from 'reactstrap';
import { Modal, ModalHeader, ModalBody, ModalFooter, NavLink } from 'reactstrap';
import { Card, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import PlotPowerAxisTiny from '../../../content-parts/PlotPowerAxisTiny';
import { GetTools } from '../../../../utils/toolUtils';
import { TryParseInt } from '../../../../utils/numberUtils';

type TaskEditProps = {
    task: TaskStore.Task,
    isLoading: boolean,
    reference: AssistantStore.Reference,

    handleRefSolderingChange: (id: number) => void,
    updateTask: (task: TaskStore.Task) => void,
    deleteTask: (id: number) => void
}

interface IState {
    task: TaskStore.Task;
    showDeleteModal: boolean;
}

class TaskEditView extends React.PureComponent<TaskEditProps, IState> {
    constructor(props: TaskEditProps) {
        super(props);

        this.state = {
            task: props.task,
            showDeleteModal: false
        };
    }

    public render() {
        const getListTools = () => {
            let tools = GetTools();
            return (
                <Input type="select" onChange={(e) => handleToolChange(e.target.value)}>
                    {Object.entries(tools).map(([key, value]) => (
                        <option value={key}>{value}</option>
                    ))}
                </Input>
            )
        }

        const getReferenceSoldering = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            if (this.props.reference.id <= 0) {
                return "Soldering reference undefined.";
            }

            return (
                <PlotPowerAxisTiny
                    plotData={this.props.reference.power}
                />
            )
        }

        const getDeleteButton = () => {
            if (this.props.task.timesPerformed > 0) {
                return "";
            }

            return (
                <Button outline color="primary" size="sm" className="m-2">
                    <a href="#" className="p-0" onClick={(event) => handleDeleteClick(event)}>
                        <FontAwesomeIcon icon={faTrashAlt} className="pr-1" /> Delete task
                    </a>
                </Button>
            )
        }

        const handleNameChange = (value: string) => {
            let newTask = this.state.task;
            newTask.name = value;

            this.setState({
                task: newTask
            });
        }

        const handleToolChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            let newTask = this.state.task;
            newTask.workConfig.tool = nValue;

            this.setState({
                task: newTask
            });
        }

        const handleModifySelecTempChange = (value: boolean) => {
            let newTask = this.state.task;
            newTask.workConfig.changeSelectedTemp = value;

            this.setState({
                task: newTask
            });
        }

        const handleSelecTempChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            nValue *= 9;

            let newTask = this.state.task;
            newTask.workConfig.selectedTemp = nValue;

            this.setState({
                task: newTask
            });
        }

        const handleModifySolderingRefChange = (value: boolean) => {
            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        changeSolderingReference: value
                    }
                }
            }));
        }

        const handleSolderingRefChange = (value: string) => {
            let nValue = TryParseInt(value, null);

            if (nValue == null) {
                return
            }

            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        solderingReferenceId: nValue
                    }
                }
            }));
        }

        const handleQualityControlChange = (value: boolean) => {
            this.setState(prevState => ({
                task: {
                    ...prevState.task,
                    workConfig: {
                        ...prevState.task.workConfig,
                        qualityControl: value
                    }
                }
            }));
        }

        const handleConfirmClick = () => {
            if (this.state.task === undefined) {
                return;
            }

            this.props.updateTask(this.state.task);
        };

        const handleDeleteClick = (event: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
            event.preventDefault();
            this.setState({
                showDeleteModal: true
            });
        };

        const handleDeleteConfirmClick = () => {
            this.props.deleteTask(this.state.task.id);
            this.setState({
                showDeleteModal: false
            });
        };

        const handleDeleteCancelClick = () => {
            this.setState({
                showDeleteModal: false
            });
        };

        return (
            <Container>
                <Row>
                    <h1>{this.state.task.name}</h1>
                </Row>
                <Row className="mb-1">
                    {getDeleteButton()}
                </Row>
                <Row>
                    <Card>
                        <CardBody>
                            <Row>
                                <Col xs="8">
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">Name:</Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                defaultValue={this.state.task.name}
                                                onChange={(e) => handleNameChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">Tool:</Col>
                                        <Col>{getListTools()}</Col>
                                    </Row>
                                    <hr />
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeSelectedTemp"
                                                label="Selected temperature"
                                                defaultChecked={this.state.task.workConfig.changeSelectedTemp}
                                                onChange={(e) => handleModifySelecTempChange(e.target.checked)} />
                                        </Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                pattern="[0-9]*"
                                                defaultValue={this.state.task.workConfig.selectedTemp / 9}
                                                onChange={(e) => handleSelecTempChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeSolderingReference"
                                                label="Soldering reference code"
                                                defaultChecked={this.state.task.workConfig.changeSolderingReference}
                                                onChange={(e) => handleModifySolderingRefChange(e.target.checked)} />
                                        </Col>
                                        <Col>
                                            <Input
                                                type="text"
                                                defaultValue={this.state.task.workConfig.solderingReferenceId}
                                                onChange={(e) => handleSolderingRefChange(e.target.value)} />
                                        </Col>
                                    </Row>
                                    <hr />
                                    <Row form className="mb-2">
                                        <Col sm={3} className="p-1">
                                            <CustomInput
                                                type="checkbox"
                                                id="taskChangeQualityControl"
                                                label="Visualize quality control"
                                                defaultChecked={this.state.task.workConfig.qualityControl}
                                                onChange={(e) => handleQualityControlChange(e.target.checked)} />
                                        </Col>
                                    </Row>
                                </Col>
                                <Col xs="4">
                                    {getReferenceSoldering()}
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Row>
                <Row>
                    <Button outline color="primary" size="sm" className="m-2" onClick={() => handleConfirmClick()}>
                        Confirm
                    </Button>

                    <Button color="primary" size="sm" className="m-2">
                        <NavLink
                            tag={Link}
                            className="text-white p-0"
                            to={"/qst/task/" + this.state.task.id + "/detail"}>Cancel</NavLink>
                    </Button>
                </Row>

                <Modal isOpen={this.state.showDeleteModal} toggle={() => handleDeleteCancelClick()}>
                    <ModalHeader toggle={() => handleDeleteCancelClick()}>Delete task</ModalHeader>
                    <ModalBody>Are you sure to delete the task? This action can not be undone.</ModalBody>
                    <ModalFooter>
                        <Button outline color="primary" onClick={() => handleDeleteConfirmClick()}>Confirm</Button>{' '}
                        <Button color="primary" onClick={() => handleDeleteCancelClick()}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </Container>
        );
    }
};

export default TaskEditView;
