import * as React from 'react';
import { Spinner } from 'reactstrap';
import * as StationStore from '../../../store/Station';
import { ToolToString } from '../../../utils/toolUtils';

type ProcessOptimizationProps = {
    isLoading: boolean,
    stationControllers: StationStore.StationController[],
    handleStationClick: (stationControllerId: string, station: StationStore.Station, port: number, tool: number) => void
}

class StationsPortList extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getListOptionsStationControllers = () => {
            if (this.props.isLoading) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            let count = this.props.stationControllers.reduce(((total, x) =>
                total + x.stations.length
            ), 0);

            if (count === 0) {
                return "No stations connected.";
            }

            let options =
                this.props.stationControllers
                    .map(sc => (
                        sc.stations.map(s => (
                            s.ports
                                .map(p => (
                                    <tr className="pointer" onClick={() => this.props.handleStationClick(sc.id, s, p.id, p.tool)}>
                                        <td>{s.name} [{s.model}]</td>
                                        <td>{p.id + 1}</td>
                                        <td>{ToolToString(p.tool)}</td>
                                    </tr>
                                ))
                        ))
                    ))

            return (
                <table className="table table-hover table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Station name</th>
                            <th scope="col">Port</th>
                            <th scope="col">Tool</th>
                        </tr>
                    </thead>
                    <tbody>
                        {options}
                    </tbody>
                </table>
            )
        };

        return (
            getListOptionsStationControllers()
        );
    }
};

export default StationsPortList;
