import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { ResponsiveContainer, LineChart, Line, Legend, XAxis, YAxis, CartesianGrid, ReferenceArea } from 'recharts';
import { CartridgeImagePathByFullName } from '../../../../utils/cartridgeUtils';
import { IOptimizedSoldering } from './data/IOptimizedSoldering';

type ProcessOptimizationProps = {
    isLoading: boolean,
    optimizedSoldering: IOptimizedSoldering,
    selectedCartridge: string
}

class ProcessOptimizationStep_Performance extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getPlot = (tempRecommended: number, tempDurability: number, tempPerformance: number) => {
            return (
                <ResponsiveContainer width='100%' aspect={2.5}>
                    <LineChart
                        data={this.props.optimizedSoldering.points}
                        margin={{
                            left: 10,
                            right: 0
                        }}
                    >
                        <CartesianGrid stroke="#e6e6e6" />
                        <Legend />

                        <XAxis
                            dataKey="temperature"
                            height={50}
                            padding={{ left: 30, right: 30 }}
                            label="Temperature"
                            tickFormatter={(value) => Math.floor(value / 9)}
                            domain={[250 * 9, 400 * 9]}
                            ticks={[250 * 9, 300 * 9, 350 * 9, 400 * 9]}
                            type="number" />
                        <YAxis
                            yAxisId="left"
                            orientation="left"
                            tickFormatter={(value) => (value * 100) + "%"}
                            domain={[0, 3]}
                            ticks={[1, 2, 3]}
                            type="number"
                            label={{
                                value: "Tip Life",
                                angle: -90,
                                position: "center",
                                dx: -30
                            }} />
                        <YAxis
                            yAxisId="right"
                            orientation="right"
                            label={{
                                value: "Solderings / minute",
                                angle: 90,
                                position: "center",
                                dx: 10
                            }} />

                        <ReferenceArea
                            yAxisId="left"
                            x1={280 * 9}
                            x2={tempDurability + (tempRecommended - tempDurability) / 2}
                            y1={0} y2={3}
                            fill="#ebebff" />
                        <ReferenceArea
                            yAxisId="left"
                            x1={tempDurability + (tempRecommended - tempDurability) / 2}
                            x2={tempRecommended + (tempPerformance - tempRecommended) / 2}
                            y1={0} y2={3}
                            fill="#fff8e1" />
                        <ReferenceArea
                            yAxisId="left"
                            x1={tempRecommended + (tempPerformance - tempRecommended) / 2}
                            x2={400 * 9}
                            y1={0} y2={3}
                            fill="#ffebeb" />

                        <Line name="Tip Life" dataKey="tipLife" yAxisId="left" type="monotone" stroke="#dc3545" dot={false} />
                        <Line name="Solderings / minute" dataKey="rateSoldMin" yAxisId="right" type="monotone" dot={false} />
                    </LineChart>
                </ResponsiveContainer>
            );
        }

        const getOptimizedSolderingPlotCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>;
            }
            else {
                cardBody = getPlot(
                    this.props.optimizedSoldering.recommended.temperature,
                    this.props.optimizedSoldering.durability.temperature,
                    this.props.optimizedSoldering.performance.temperature)
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        const getDetailsCard = () => {
            let cardBody;
            if (this.props.isLoading) {
                cardBody =
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>;
            }
            else {
                cardBody =
                    <React.Fragment>
                        <Row>
                            <Col xs="6">
                                <CardText>Cartridge: <strong>{this.props.selectedCartridge}</strong></CardText>
                            </Col>
                            <Col xs="6">
                                <img src={CartridgeImagePathByFullName(this.props.selectedCartridge)} alt={this.props.selectedCartridge} />
                            </Col>
                        </Row>

                        <hr />
                        <CardText>
                            <strong>Optimized:</strong>
                        </CardText>
                        <CardText>Tip life: {this.props.optimizedSoldering.recommended.tipLife * 100}%</CardText>
                        <CardText>Solderings / minute: {this.props.optimizedSoldering.recommended.rateSoldMin}</CardText>
                        <CardText>Temperature: {Math.floor(this.props.optimizedSoldering.recommended.temperature / 9)} C</CardText>
                    </React.Fragment>
            }

            return (
                <Card>
                    <CardBody>
                        {cardBody}
                    </CardBody>
                </Card>
            )
        }

        return (
            <React.Fragment>
                <Row>
                    <Col xs="8" className="pl-0 pr-0">
                        {getOptimizedSolderingPlotCard()}
                    </Col>
                    <Col xs="4" className="d-flex flex-column">
                        {getDetailsCard()}
                    </Col>
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_Performance;
