import * as React from 'react';
import { Container } from 'reactstrap';
import { Col, Row } from 'reactstrap';
import StationsUpdateCard from '../../../content-parts/CardLinkApp/StationsUpdateCard';

const Maintenance = () => {
        return (
            <Container>
                <Row className="content-center" xs={1} sm={2} md={3}>
                    <Col>
                        <StationsUpdateCard/>
                    </Col>
                </Row>
            </Container>
        );
};

export default Maintenance;
