import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Card, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle } from '@fortawesome/free-solid-svg-icons'
import { GetPortStatus2String, GetPortStatus2Lights } from '../../../utils/portUtils';
import './PortStatusCard.css';

type PortStatusCardProps = {
    portData: IRealTimeData
}

interface IRealTimeData {
    temperature: number,
    power: number,
    toolStatus: number,
    isPortEnabled: boolean,
    isCalibrating: boolean,
    isSoldering: boolean,
    tenthsOfSecondSoldering: number
}

class PortStatusCard extends React.PureComponent<PortStatusCardProps> {
    public render() {
        let temperature = this.props.portData === undefined ? 0 : this.props.portData.temperature;
        let power = this.props.portData === undefined ? 0 : this.props.portData.power;
        let toolStatus = this.props.portData === undefined ? 0 : this.props.portData.toolStatus;
        let isPortEnabled = this.props.portData === undefined ? false : this.props.portData.isPortEnabled;
        let isCalibrating = this.props.portData === undefined ? false : this.props.portData.isCalibrating;
        let isSoldering = this.props.portData === undefined ? false : this.props.portData.isSoldering;

        let circleLight = "circle-light-" + GetPortStatus2Lights(
            toolStatus,
            isCalibrating,
            isSoldering,
            isPortEnabled);

        return (
            <Card>
                <CardBody>
                    <Row>
                        <Col xs="auto">
                            Temperature:{' '}
                            <span className="temp-value"> {Math.floor(temperature / 9)} C</span>
                        </Col>
                        <Col>
                            Power:{' '}
                            <span className="power-value">{Math.floor(power / 10)} %</span>
                        </Col>
                        <Col className="text-right">
                            <FontAwesomeIcon icon={faCircle} className={circleLight} />
                            <strong className="pl-1">{GetPortStatus2String(
                                toolStatus,
                                isCalibrating,
                                isSoldering,
                                isPortEnabled
                            )}</strong>
                        </Col>
                    </Row>
                </CardBody>
            </Card>
        );
    }
};

export default PortStatusCard;
