import * as React from 'react';
import { Col, Row } from 'reactstrap';
import { Input } from 'reactstrap';
import { Card, CardText, CardBody } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTrashAlt } from '@fortawesome/free-regular-svg-icons'
import { LoadCartridges, ICartridge } from '../../../../utils/cartridgeUtils';
import Plot2AxisTiny from '../../../content-parts/Plot2AxisTiny';
import PortStatusCard from '../../../content-parts/PortStatusCard';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';

type ProcessOptimizationProps = {
    selectedTool: number,
    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],

    handleCartridgeChange: (value: string) => void,
    handleDeleteRealTimeData: (value: string) => void
}

interface IState {
    cartridges: ICartridge[],
    cartridgeImagePath: string,
    cartridgeName: string
}

class ProcessOptimizationStep_CartridgeTest extends React.PureComponent<ProcessOptimizationProps, IState> {
    constructor(props: ProcessOptimizationProps) {
        super(props);

        let cartridgeImagePath = "";
        let cartridgeName = "";
        let cartridges = LoadCartridges();
        let defaultCartridge = cartridges.find(x => x.isDefault && x.tool === this.props.selectedTool);

        if (defaultCartridge !== undefined) {
            cartridgeImagePath = defaultCartridge.img;
            cartridgeName = defaultCartridge.fullName;
            this.props.handleCartridgeChange(defaultCartridge.fullName);
        }

        this.state = {
            cartridges: cartridges,
            cartridgeImagePath: cartridgeImagePath,
            cartridgeName: cartridgeName
        };
    }

    public render() {
        const getPortStatus = () => {
            return <PortStatusCard portData={this.props.lastRealTimeData} />;
        }

        const getCartridges = () => {
            let cartridges = this.state.cartridges
                .filter(x => x.tool === this.props.selectedTool)
                .map(x => (
                    <option selected={x.isDefault}>{x.fullName}</option>
                ));

            return cartridges;
        }

        const getConfigurationCard = () => {
            return (
                <Card>
                    <CardBody>
                        <Row>
                            <Col xs="8">
                                <CardText>
                                    <Input
                                        type="select"
                                        onChange={(e) => handleCartridgeChange(e.target.value)}
                                    >
                                        {getCartridges()}
                                    </Input>
                                </CardText>
                            </Col>
                            <Col xs="4" className="text-center">
                                <img src={this.state.cartridgeImagePath} alt={this.state.cartridgeName} />
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            )
        }

        const getRegisteredPlots = () => {
            if (this.props.detectedSolderings.length === 0) {
                return (
                    <CardText>
                        Performs at least 2 solderings with different cartridges.
                    </CardText>
                )
            }

            let registeredPlots = this.props.detectedSolderings.map(x => {
                let data: IRealTimeData[];
                data = x.data.map(x => {
                    return {
                        temperature: x.temperature,
                        power: x.power,
                        toolStatus: x.toolStatus,
                        isPortEnabled: x.isPortEnabled,
                        isCalibrating: x.isCalibrating,
                        isSoldering: x.isSoldering,
                        tenthsOfSecondSoldering: x.tenthsOfSecondSoldering
                    }
                });

                return (
                    <Col>
                        <Card className="card-v2">
                            <CardBody>
                                <Row>
                                    <Col xs="9" className="pr-0">
                                        <CardText>Time: <strong>{x.solderingResult.tenthsOfSecondSoldering / 10} seconds</strong></CardText>
                                        <CardText>Cartridge: <strong>{x.cartridge}</strong></CardText>
                                    </Col>
                                    <Col xs="3" className="text-right">
                                        <a className="pointer" onClick={() => this.props.handleDeleteRealTimeData(x.date)}>
                                            <FontAwesomeIcon icon={faTrashAlt} />
                                        </a>
                                    </Col>
                                </Row>
                                <Row>
                                    <Plot2AxisTiny
                                        plotData={data}
                                        counterTenthSecond={x.data.length}
                                        interval={Math.floor(x.data.length / 2)}
                                    />
                                </Row>
                            </CardBody>
                        </Card>
                    </Col>);
            });

            return (
                <Row xs={1} sm={2} md={5}>
                    {registeredPlots}
                </Row>
            )
        }

        const getRegisteredPlotsCard = () => {
            return (
                <Card>
                    <CardBody>
                        {getRegisteredPlots()}
                    </CardBody>
                </Card>
            )
        }

        const handleCartridgeChange = (value: string) => {
            this.props.handleCartridgeChange(value);
            let cartridge = this.state.cartridges.find(x => x.fullName === value);
            let cartridgeImagePath = cartridge === undefined ? "" : cartridge.img;
            let cartridgeName = cartridge === undefined ? "" : cartridge.fullName;

            this.setState({
                cartridgeImagePath: cartridgeImagePath,
                cartridgeName: cartridgeName
            });
        }

        return (
            <React.Fragment>
                <Row className="mb-3">
                    {getPortStatus()}
                </Row>
                <Row className="mb-3">
                    {getConfigurationCard()}
                </Row>
                <Row>
                    {getRegisteredPlotsCard()}
                </Row>
            </React.Fragment>
        );
    }
};

export default ProcessOptimizationStep_CartridgeTest;
