import * as React from 'react';
import { Container } from 'reactstrap';
import { Row } from 'reactstrap';
import { Button } from 'reactstrap';
import * as StationStore from '../../../../store/Station';
import MultiStepNav from '../../../content-parts/MultiStepNav';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingData } from './data/ISolderingData';
import { IOptimizedSoldering } from './data/IOptimizedSoldering';
import ProcessOptimizationStep_Station from './ProcessOptimizationStep_Station';
import ProcessOptimizationStep_Temp from './ProcessOptimizationStep_Temp';
import ProcessOptimizationStep_ProcessParameters from './ProcessOptimizationStep_ProcessParameters';
import ProcessOptimizationStep_CartridgeTest from './ProcessOptimizationStep_CartridgeTest';
import ProcessOptimizationStep_CartridgeResult from './ProcessOptimizationStep_CartridgeResult';
import ProcessOptimizationStep_TempTest from './ProcessOptimizationStep_TempTest';
import ProcessOptimizationStep_Performance from './ProcessOptimizationStep_Performance';

type ProcessOptimizationProps = {
    currentStep: number,
    isLoading: boolean,

    stationControllers: StationStore.StationController[],
    handleStationClick: (stationControllerId: string, station: StationStore.Station, port: number, tool: number) => void,
    handleSelecTempChange: (temperature: string) => void,
    handleCartridgeChange: (value: string) => void,
    handleOffsetTimeChange: (temperature: string) => void,
    handleDeleteRealTimeData: (value: string) => void,
    handleNextClick: () => void,

    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTool: number,
    selectedTemperature: number,
    selectedCartridge: string,
    selectedOffsetTime: number,

    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,

    detectedSolderings: ISolderingData[],
    fasterSoldering: ISolderingData,
    optimizedSoldering: IOptimizedSoldering
}

class ProcessOptimizationView extends React.PureComponent<ProcessOptimizationProps> {
    public render() {
        const getButtons = () => {
            let isValid =
                (this.props.currentStep === 1 && this.props.selectedStation.id !== "") ||
                (this.props.currentStep === 2 && this.props.selectedTemperature > 0) ||
                (this.props.currentStep === 3) ||
                (this.props.currentStep === 4 && this.props.detectedSolderings.length > 1) ||
                (this.props.currentStep === 5) ||
                (this.props.currentStep === 6 && this.props.detectedSolderings.length > 1) ||
                (this.props.currentStep === 7);

            let nextButton = !isValid ? "" :
                <Button
                    color="primary"
                    size="sm"
                    className="m-2"
                    onClick={this.props.handleNextClick}
                >{this.props.currentStep === 7 ? "Start again" : "Continue"}</Button>

            return (
                <React.Fragment>
                    {nextButton}
                </React.Fragment>)
        }

        const getSteps = () => {
            if (this.props.currentStep === 1) {
                return (
                    <ProcessOptimizationStep_Station
                        isLoadingStations={this.props.isLoading}
                        stationControllers={this.props.stationControllers}
                        handleStationClick={this.props.handleStationClick}
                    />
                )
            }

            if (this.props.currentStep === 2) {
                return (
                    <ProcessOptimizationStep_Temp
                        selectedTemperature={this.props.selectedTemperature}
                        handleSelecTempChange={this.props.handleSelecTempChange}
                    />
                )
            }

            if (this.props.currentStep === 3) {
                return (
                    <ProcessOptimizationStep_ProcessParameters
                        selectedOffsetTime={this.props.selectedOffsetTime}
                        handleOffsetTimeChange={this.props.handleOffsetTimeChange}
                    />
                )
            }

            if (this.props.currentStep === 4) {
                return (
                    <ProcessOptimizationStep_CartridgeTest
                        selectedTool={this.props.selectedTool}
                        lastRealTimeData={this.props.lastRealTimeData}
                        counterTenthSecond={this.props.counterTenthSecond}

                        detectedSolderings={this.props.detectedSolderings}

                        handleCartridgeChange={this.props.handleCartridgeChange}
                        handleDeleteRealTimeData={this.props.handleDeleteRealTimeData}
                    />
                )
            }

            if (this.props.currentStep === 5) {
                return (
                    <ProcessOptimizationStep_CartridgeResult
                        isLoading={this.props.isLoading}
                        detectedSolderings={this.props.detectedSolderings}
                        fasterSoldering={this.props.fasterSoldering}

                        handleCartridgeChange={this.props.handleCartridgeChange}
                    />
                )
            }

            if (this.props.currentStep === 6) {
                return (
                    <ProcessOptimizationStep_TempTest
                        isLoading={this.props.isLoading}

                        selectedStation={this.props.selectedStation}
                        selectedPort={this.props.selectedPort}
                        selectedTemperature={this.props.selectedTemperature}
                        selectedCartridge={this.props.selectedCartridge}

                        lastRealTimeData={this.props.lastRealTimeData}
                        counterTenthSecond={this.props.counterTenthSecond}

                        detectedSolderings={this.props.detectedSolderings}

                        handleSelecTempChange={this.props.handleSelecTempChange}
                        handleDeleteRealTimeData={this.props.handleDeleteRealTimeData}
                    />
                )
            }

            if (this.props.currentStep === 7) {
                return (
                    <ProcessOptimizationStep_Performance
                        isLoading={this.props.isLoading}

                        optimizedSoldering={this.props.optimizedSoldering}
                        selectedCartridge={this.props.selectedCartridge}
                    />
                )
            }
        }

        return (
            <Container>
                <Row className="mb-1">
                    <h1>Process optimization</h1>
                </Row>
                <Row className="content-center mb-3">
                    <MultiStepNav
                        labels={[
                            "Station selection",
                            "Temperature selection",
                            "Process parameters",
                            "Cartridge test",
                            "Cartridge result",
                            "Temperature test",
                            "Performance result"
                        ]}
                        current={this.props.currentStep - 1}
                    />
                </Row>
                {getSteps()}
                <Row>
                    {getButtons()}
                </Row>
            </Container>
        );
    }
};

export default ProcessOptimizationView;
