import * as React from 'react';
import { connect } from 'react-redux';
import { ApplicationState } from '../../../../store';
import * as StationStore from '../../../../store/Station';
import AssistantService from '../../../../services/assistant.service';
import ProcessOptimizationService from '../../../../services/processOptimization.service';
import StationService from '../../../../services/station.service';
import WorkService from '../../../../services/work.service';
import { TryParseInt } from '../../../../utils/numberUtils';
import { IRealTimeData } from './data/IRealTimeData';
import { ISolderingResult } from './data/ISolderingResult';
import { ISolderingData } from './data/ISolderingData';
import { IOptimizedSoldering } from './data/IOptimizedSoldering';
import ProcessOptimizationView from './ProcessOptimizationView';

type ProcessOptimizationProps = {
    stationControllers: StationStore.StationController[]
} & typeof StationStore.actionCreators

interface IState {
    currentStep: number,
    isLoading: boolean,
    timerSelectedTemp: number,

    selectedStationControllerId: string,
    selectedStation: StationStore.Station,
    selectedPort: number,
    selectedTool: number,
    selectedTemperature: number,
    selectedCartridge: string,
    selectedOffsetTime: number,

    queueId: number,
    registeredData: IRealTimeData[],
    lastRealTimeData: IRealTimeData,
    counterTenthSecond: number,
    readyGetMoreData: boolean,

    wasSoldering: boolean,
    detectedSolderings: ISolderingData[],
    detectedSolderingsToRemove: string[],
    fasterSoldering: ISolderingData,
    optimizedSoldering: IOptimizedSoldering
}

const WAIT_INTERVAL_SET_SELECTED_TEMP = 1000;
const MAX_POINTS_REGISTERED_DATA = 50;
const INTERVAL_REFRESH_PLOT_MILLISCONDS = 200;

class ProcessOptimization extends React.PureComponent<ProcessOptimizationProps, IState> {
    constructor(props: ProcessOptimizationProps) {
        super(props);

        this.state = {
            currentStep: 1,
            isLoading: true,
            timerSelectedTemp: -1,

            selectedStationControllerId: "",
            selectedStation: {
                id: "",
                name: "",
                software: "",
                hardware: "",
                model: "",
                isQSTMode: false,
                ports: []
            },
            selectedPort: -1,
            selectedTool: 0,
            selectedTemperature: 350 * 9,
            selectedCartridge: "",
            selectedOffsetTime: 5,

            queueId: -1,
            registeredData: [],
            lastRealTimeData: {
                temperature: 0,
                power: 0,
                toolStatus: 0,
                isPortEnabled: true,
                isCalibrating: false,
                isSoldering: false,
                tenthsOfSecondSoldering: 0
            },
            counterTenthSecond: 0,
            readyGetMoreData: true,

            wasSoldering: false,
            detectedSolderings: [],
            detectedSolderingsToRemove: [],
            fasterSoldering: {
                data: [],
                solderingResult: {
                    energy: 0,
                    similarity: 0,
                    tenthsOfSecondSoldering: 0
                },
                cartridge: "",
                selectedTemperature: 0,
                date: ""
            },
            optimizedSoldering: {
                points: [],
                recommended: {
                    temperature: 0,
                    tipLife: 0,
                    rateSoldMin: 0
                },
                performance: {
                    temperature: 0,
                    tipLife: 0,
                    rateSoldMin: 0
                },
                durability: {
                    temperature: 0,
                    tipLife: 0,
                    rateSoldMin: 0
                }
            }
        };

        this.searchStations(this.props);

        setInterval(() => {
            if (this.state.queueId > -1 &&
                this.state.readyGetMoreData) {

                this.setState({
                    readyGetMoreData: false
                })

                this.applyDetectedSolderingsToRemove();

                WorkService
                    .getRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.queueId)
                    .then((values: IRealTimeData[]) => {
                        var registeredData = this.state.registeredData;
                        var lastRealTimeData = this.state.lastRealTimeData;
                        var counterTenthSecond = this.state.counterTenthSecond;

                        var wasSoldering = this.state.wasSoldering;
                        var detectedSolderings = this.state.detectedSolderings;

                        if (Array.isArray(values)) {
                            values.forEach(x => {

                                // raise flag
                                if (x.isSoldering && !wasSoldering) {
                                    let data: IRealTimeData[] = [];
                                    let nDataSoldering = Math.min(registeredData.length, x.tenthsOfSecondSoldering);

                                    for (var i = registeredData.length - nDataSoldering; i < registeredData.length; i++) {
                                        data.push(registeredData[i]);
                                    }

                                    let soldering = {
                                        data: data,
                                        solderingResult: {
                                            energy: 0,
                                            similarity: 0,
                                            tenthsOfSecondSoldering: 0
                                        },
                                        cartridge: this.state.selectedCartridge,
                                        selectedTemperature: this.state.selectedTemperature,
                                        date: (new Date()).toString()
                                    };

                                    detectedSolderings = detectedSolderings.concat(soldering);
                                }

                                // down flag
                                if (!x.isSoldering && wasSoldering) {
                                    AssistantService
                                        .getSolderingResult(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort)
                                        .then((x: ISolderingResult) => {
                                            let soldering = detectedSolderings[detectedSolderings.length - 1];
                                            let solderingResult = soldering.solderingResult;

                                            solderingResult.energy = x.energy;
                                            solderingResult.tenthsOfSecondSoldering = x.tenthsOfSecondSoldering;
                                            soldering.solderingResult = solderingResult;
                                            soldering.cartridge = this.state.selectedCartridge;

                                            detectedSolderings[detectedSolderings.length - 1] = soldering;
                                        });
                                }

                                // soldering
                                if (x.isSoldering) {
                                    let soldering = detectedSolderings[detectedSolderings.length - 1];
                                    let data = soldering.data;

                                    data.push(x);
                                    soldering.data = data;

                                    let solderingResult = soldering.solderingResult;
                                    solderingResult.tenthsOfSecondSoldering = soldering.data.length;
                                    soldering.solderingResult = solderingResult;

                                    detectedSolderings[detectedSolderings.length - 1] = soldering;
                                }

                                registeredData = registeredData.concat(x);
                                registeredData.splice(0, registeredData.length - MAX_POINTS_REGISTERED_DATA);
                                lastRealTimeData = x;
                                counterTenthSecond++;

                                wasSoldering = x.isSoldering;
                            })
                        }

                        this.setState({
                            registeredData: registeredData,
                            lastRealTimeData: lastRealTimeData,
                            counterTenthSecond: counterTenthSecond,
                            readyGetMoreData: true,

                            wasSoldering: wasSoldering,
                            detectedSolderings: detectedSolderings
                        });
                    })
            }
        }, INTERVAL_REFRESH_PLOT_MILLISCONDS);
    }

    searchStations(props: ProcessOptimizationProps) {
        props.deleteAllStationControllers();
        this.setState({
            isLoading: true
        })

        StationService
            .getStations()
            .then(scs => {
                if (Array.isArray(scs)) {
                    scs.forEach((sc: StationStore.StationController) => {
                        props.createStationController(sc);

                        sc.stations.forEach((s: StationStore.Station) =>
                            props.createStation(s, sc.id)
                        )
                    })
                }

                this.setState({
                    isLoading: false
                })
            });
    }

    applyDetectedSolderingsToRemove() {
        if (this.state.detectedSolderingsToRemove.length === 0) {
            return;
        }

        this.setState((state: IState, props: ProcessOptimizationProps) => {
            return {
                detectedSolderings:
                    state.detectedSolderings
                        .filter(x => !state.detectedSolderingsToRemove
                            .some(r => r === x.date)),
                detectedSolderingsToRemove: []
            };
        });
    }

    public render() {
        const handleStationClick = (stationControllerId: string, station: StationStore.Station, port: number, tool: number) => {
            this.setState({
                selectedStationControllerId: stationControllerId,
                selectedStation: station,
                selectedPort: port,
                selectedTool: tool
            })

            setSelectedTemperatureRequest(stationControllerId, station.id, port, this.state.selectedTemperature);
        }

        const handleSelecTempChange = (value: string) => {
            let nValue = TryParseInt(value, -1);

            if (nValue < 0) {
                return;
            }

            nValue *= 9

            this.setState({
                selectedTemperature: nValue
            });

            setSelectedTemperatureRequest(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort, nValue);
        }

        const setSelectedTemperatureRequest = (stationControllerId: string, stationId: string, port: number, temperature: number) => {
            if (stationId === "" || port === -1) {
                return;
            }

            if (this.state.timerSelectedTemp > 0)
                window.clearTimeout(this.state.timerSelectedTemp);

            this.setState({
                timerSelectedTemp: setTimeout(setSelectedTemperature, WAIT_INTERVAL_SET_SELECTED_TEMP, stationControllerId, stationId, port, temperature)
            });
        }

        const setSelectedTemperature = (stationControllerId: string, stationId: string, port: number, temperature: number) => {
            StationService.setSelectedTemperature(stationControllerId, stationId, port, temperature);
        }

        const handleCartridgeChange = (value: string) => {
            this.setState({
                selectedCartridge: value
            });
        }

        const handleOffsetTimeChange = (value: string) => {
            let nValue = TryParseInt(value, -1);

            if (nValue < 0) {
                return;
            }

            this.setState({
                selectedOffsetTime: nValue
            });
        }

        const handleDeleteRealTimeData = (value: string) => {
            this.setState((state: IState, props: ProcessOptimizationProps) => {
                return {
                    detectedSolderingsToRemove: state.detectedSolderingsToRemove.concat(value)
                };
            });
        }

        const handleNextClick = () => {
            var queueId = this.state.queueId;

            if (queueId !== -1) {
                this.setState({
                    queueId: -1
                })
                WorkService.stopRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, queueId);
            }


            if (this.state.currentStep === 1) {
                this.setState({
                    currentStep: 2
                })
            }
            else if (this.state.currentStep === 2) {
                this.setState({
                    currentStep: 3
                })
            }
            else if (this.state.currentStep === 3) {
                this.setState({
                    currentStep: 4,
                    counterTenthSecond: 0,

                    wasSoldering: false,
                    detectedSolderings: []
                })

                WorkService
                    .startRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort)
                    .then((value) => {
                        this.setState({
                            queueId: value
                        })
                    })
            }
            else if (this.state.currentStep === 4) {
                this.setState({
                    currentStep: 5,
                    isLoading: true
                })

                ProcessOptimizationService
                    .getBetterOptimizationByCartridge(this.state.detectedSolderings)
                    .then((value) => {
                        this.setState({
                            isLoading: false,
                            fasterSoldering: value
                        })
                    })
            }
            else if (this.state.currentStep === 5) {
                this.setState({
                    currentStep: 6,
                    counterTenthSecond: 0,

                    wasSoldering: false,
                    detectedSolderings: []
                })

                WorkService
                    .startRealTimeData(this.state.selectedStationControllerId, this.state.selectedStation.id, this.state.selectedPort)
                    .then((value) => {
                        this.setState({
                            queueId: value
                        })
                    })
            }
            else if (this.state.currentStep === 6) {
                this.setState({
                    currentStep: 7,
                    isLoading: true
                })

                let solderings = this.state.detectedSolderings.map(x => {
                    x.solderingResult.tenthsOfSecondSoldering += this.state.selectedOffsetTime * 10
                    return x;
                });

                ProcessOptimizationService
                    .getBetterOptimizationByTemperature(solderings)
                    .then((value) => {
                        this.setState({
                            isLoading: false,
                            optimizedSoldering: value
                        })
                    })
            }
            else if (this.state.currentStep === 7) {
                this.setState({
                    currentStep: 1,
                    isLoading: true
                })

                this.searchStations(this.props);
            }
        }

        return (
            <ProcessOptimizationView
                currentStep={this.state.currentStep}
                isLoading={this.state.isLoading}

                stationControllers={this.props.stationControllers}
                handleStationClick={handleStationClick}
                handleSelecTempChange={handleSelecTempChange}
                handleCartridgeChange={handleCartridgeChange}
                handleOffsetTimeChange={handleOffsetTimeChange}
                handleDeleteRealTimeData={handleDeleteRealTimeData}
                handleNextClick={handleNextClick}

                selectedStation={this.state.selectedStation}
                selectedPort={this.state.selectedPort}
                selectedTool={this.state.selectedTool}
                selectedTemperature={this.state.selectedTemperature}
                selectedCartridge={this.state.selectedCartridge}
                selectedOffsetTime={this.state.selectedOffsetTime}

                lastRealTimeData={this.state.lastRealTimeData}
                counterTenthSecond={this.state.counterTenthSecond}

                detectedSolderings={this.state.detectedSolderings}
                fasterSoldering={this.state.fasterSoldering}
                optimizedSoldering={this.state.optimizedSoldering}
            />
        );
    }
};

const mapStateToProps = (state: ApplicationState) => {
    return {
        stationControllers: state.station.stationControllers
    };
};

export default connect(
    mapStateToProps,
    StationStore.actionCreators
)(ProcessOptimization);
