import * as React from 'react';
import { connect } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Spinner } from 'reactstrap';
import { ApplicationState } from '../../../../store';
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import AssistantService from '../../../../services/assistant.service';
import TasksService from '../../../../services/tasks.service';
import { TryParseInt } from '../../../../utils/numberUtils';
import TaskEditView from './TaskEditView';

type TaskEditProps =
    TaskStore.TaskState &
    typeof TaskStore.actionCreators &
    RouteComponentProps<{ id: string }>;

interface IState {
    id: number,
    isLoading: boolean,
    isLoadingRefSoldering: boolean,
    reference: AssistantStore.Reference
}

class TaskEdit extends React.PureComponent<TaskEditProps, IState> {
    constructor(props: TaskEditProps) {
        super(props);
        const id = TryParseInt(this.props.match.params['id'], 0);

        this.state = {
            id: id,
            isLoading: true,
            isLoadingRefSoldering: true,
            reference: {
                id: -1,
                name: "",
                power: [],
                cartridge: "",
                temperature: 0,
                durationTenthsSeconds: 0
            }
        };

        this.searchTask(this.props, id);
    }

    searchTask(props: TaskEditProps, id: string) {
        TasksService
            .getTaskById(id)
            .then((x: TaskStore.Task) => {
                props.createTask(x)

                this.setState({
                    isLoading: false
                })

                if (x.workConfig.changeSolderingReference) {
                    AssistantService
                        .getReferenceById(x.workConfig.solderingReferenceId)
                        .then(reference => {
                            this.setState({
                                isLoadingRefSoldering: false,
                                reference: reference
                            })
                        });
                }
                else {
                    this.setState({
                        isLoadingRefSoldering: false
                    })
                }
            });
    }

    public render() {
        const handleRefSolderingChange = (id: number) => {
            this.setState({
                isLoadingRefSoldering: true
            })

            AssistantService
                .getReferenceById(id)
                .then(reference => {
                    this.setState({
                        isLoadingRefSoldering: false,
                        reference: reference
                    })
                });
        }

        const handleUpdateTask = (task: TaskStore.Task) => {
            TasksService
                .updateTask(task)
                .then(() =>
                    this.props.history.push("/qst/task/" + this.state.id + "/detail")
                );
        }

        const handleDeleteTask = (id: number) => {
            TasksService
                .deleteTask(id)
                .then(() =>
                    this.props.history.push("/qst/task")
                );
        }

        if (this.state.isLoading) {
            return (
                <div className="text-center">
                    <Spinner color="primary" />
                </div>);
        }

        const task = this.props.tasks.find(x => x.id === this.state.id);

        if (task === undefined) {
            return "No item found.";
        }

        return (
            <TaskEditView
                task={task}
                isLoading={this.state.isLoadingRefSoldering}
                reference={this.state.reference}

                handleRefSolderingChange={handleRefSolderingChange}
                updateTask={handleUpdateTask}
                deleteTask={handleDeleteTask}
            />
        );
    }
};

export default connect(
    (state: ApplicationState) => state.task,
    TaskStore.actionCreators
)(TaskEdit);
