import React from 'react'
import CardLinkApp from '../../CardLinkApp';

const StationsUpdateCardView = () => {
    return (
        <CardLinkApp 
            to="/maintenance/stationsupdate"
            imgPath="img/iconImg/updateStations.png"
            cardText="Update stations for better performance and better user experience."
            cardTitle="Stations update"
            alt="Stations update"
        />
    )
}

export default StationsUpdateCardView;

