import * as React from 'react';
import { Container } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Button } from 'reactstrap';
import { Row, Col } from 'reactstrap';
import { NavLink } from 'reactstrap';
import { Card, CardText, CardBody, CardTitle } from 'reactstrap';
import { Spinner } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCircle, faEye, faPen } from '@fortawesome/free-solid-svg-icons'
import * as AssistantStore from '../../../../store/Assistant';
import * as TaskStore from '../../../../store/Task';
import * as TaskHistoryStore from '../../../../store/TaskHistory';
import PlotPowerAxisTiny from '../../../content-parts/PlotPowerAxisTiny';
import ResponsivePieChart from '../../../content-parts/ResponsivePieChart';
import { Round } from '../../../../utils/numberUtils';
import { ToolToString } from '../../../../utils/toolUtils';
import PaginationNav from '../../../content-components/PaginationNav';

type TaskDetailProps = {
    task: TaskStore.Task | undefined,
    taskHistories: TaskHistoryStore.TaskHistory[],
    nTotalTaskHistories: number,
    reference: AssistantStore.Reference,

    pageSize: number,
    pageNumber: number,

    isLoadingTask: boolean,
    isLoadingHistories: boolean,
    isLoadingRefSoldering: boolean,

    handlePageClick: (pageNumber: number) => void,
    handlePreviousPageClick: () => void,
    handleNextPageClick: () => void
}

class TaskDetailView extends React.PureComponent<TaskDetailProps> {
    public render() {
        if (this.props.isLoadingTask) {
            return (
                <div className="text-center">
                    <Spinner color="primary" />
                </div>);
        }

        if (this.props.task === undefined) {
            return "No item found.";
        }

        const getDefectsChart = (correct: number, incorrect: number) => {
            const data = [
                { name: 'Correct', value: correct },
                { name: 'Incorrect', value: incorrect }
            ];

            return <ResponsivePieChart data={data} />
        }

        const getReferenceSoldering = () => {
            if (this.props.task === undefined) {
                return "";
            }

            if (!this.props.task.workConfig.changeSolderingReference) {
                return "";
            }

            if (this.props.isLoadingRefSoldering) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            return (
                <PlotPowerAxisTiny
                    plotData={this.props.reference.power}
                />
            )
        }

        const getDetailsCard = () => {
            if (this.props.task === undefined) {
                return "";
            }

            let details =
                <React.Fragment>
                    <CardText>Code: {this.props.task.id}</CardText>
                    <CardText># of times performed: {this.props.task.timesPerformed}</CardText>

                    <hr />
                    <CardText>Tool: {ToolToString(this.props.task.workConfig.tool)}</CardText>
                    {this.props.task.workConfig.changeSelectedTemp ?
                        <CardText>
                            Selected temperature: {Math.floor(this.props.task.workConfig.selectedTemp / 9)} C
                        </CardText> :
                        ""
                    }
                    {this.props.task.workConfig.changeSolderingReference ?
                        <CardText>
                            Soldering reference: {this.props.reference.name.length === 0 ? "[No name]" : this.props.reference.name}
                        </CardText> :
                        ""
                    }
                </React.Fragment>



            let body =
                this.props.task.workConfig.changeSolderingReference ?
                    <CardBody className="d-flex flex-column">
                        <Row>
                            <Col xs="6">
                                {details}
                            </Col>
                            <Col xs="6">
                                {getReferenceSoldering()}
                            </Col>
                        </Row>
                    </CardBody> :
                    <CardBody>
                        {details}
                    </CardBody>

            return (
                <Card>
                    {body}
                </Card>
            )
        }

        const getQualityCard = () => {
            if (this.props.task === undefined) {
                return "";
            }

            let correct = this.props.taskHistories.reduce(((total, x) =>
                total + x.workData.workSolderings.reduce(((total2, x2) =>
                    total2 + ((x2.solderingType === 0) ? 1 : 0)
                ), 0)
            ), 0);

            let incorrect = this.props.taskHistories.reduce(((total, x) =>
                total + x.workData.workSolderings.reduce(((total2, x2) =>
                    total2 + ((x2.solderingType !== 0) ? 1 : 0)
                ), 0)
            ), 0);

            let total = correct + incorrect;
            let percent = total === 0 ? 0 : Round((correct / total) * 100);

            return (
                <Card>
                    <CardBody>
                        {getDefectsChart(correct, incorrect)}
                        <CardText>Correct solderings: <strong>{correct}/{total} ({percent}%)</strong></CardText>
                    </CardBody>
                </Card>
            )
        }

        const getListHistoryTasks = () => {
            if (this.props.task === undefined)
                return "";

            if (this.props.isLoadingHistories) {
                return (
                    <div className="text-center">
                        <Spinner color="primary" />
                    </div>);
            }

            let items = [];

            for (const value of this.props.taskHistories) {
                let circleLight = "circle-light-" + (value.workData.workSolderings.filter(x => x.solderingType === 1).length === 0 ? "0" : "1")

                items.push(
                    <tr>
                        <td>{value.id}</td>
                        <td>{new Date(value.finishDate).toLocaleDateString('es-ES')}</td>
                        {this.props.task.workConfig.qualityControl ?
                            <td>
                                <FontAwesomeIcon icon={faCircle} className={circleLight + " pl-1"} />
                            </td> : ""
                        }
                        <td>
                            <NavLink tag={Link} className="pr-2 d-inline" to={"/qst/task/" + this.props.task.id + "/history/" + value.id}>
                                <FontAwesomeIcon icon={faEye} className="fa" />
                            </NavLink>
                        </td>
                    </tr>)
            }

            return (
                <table className="table table-sm">
                    <thead>
                        <tr>
                            <th scope="col">Id</th>
                            <th scope="col">Finished date</th>
                            {this.props.task.workConfig.qualityControl ?
                                <th scope="col">Detected defects</th> : ""
                            }
                            <th scope="col">Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {items}
                    </tbody>
                </table>
            );
        }

        const getListHistoryTasksCard = () => {
            return (
                <Card>
                    <CardBody>
                        <CardTitle>Task history ({this.props.nTotalTaskHistories} elements)</CardTitle>
                        <CardText>
                            {getListHistoryTasks()}
                        </CardText>
                    </CardBody>
                </Card>
            )
        }

        const getLayout = () => {
            if (this.props.task === undefined) {
                return "";
            }

            if (this.props.task.workConfig.qualityControl) {
                return (
                    <React.Fragment>
                        <Col xs="8" className="pl-0">
                            {getDetailsCard()}
                        </Col>
                        <Col xs="4" className="pl-0 pr-0">
                            {getQualityCard()}
                        </Col>
                    </React.Fragment>
                )
            }
            else {
                return (
                    <React.Fragment>
                        {getDetailsCard()}
                    </React.Fragment>
                )
            }
        }

        return (
            <Container>
                <Row>
                    <h1>{this.props.task.name}</h1>
                </Row>
                <Row className="mb-1">
                    <Button outline color="primary" size="sm" className="m-2">
                        <NavLink tag={Link} className="p-0" to={"/qst/task/" + this.props.task.id + "/edit"}>
                            <FontAwesomeIcon icon={faPen} className="pr-1" /> Edit task
                        </NavLink>
                    </Button>
                </Row>
                <Row className="mb-3">
                    {getLayout()}
                </Row>
                <Row className="mb-3">
                    {getListHistoryTasksCard()}
                </Row>
                <Row>
                    <PaginationNav
                        totalElements={this.props.nTotalTaskHistories}
                        pageSize={this.props.pageSize}
                        pageNumber={this.props.pageNumber}
                        handlePageClick={this.props.handlePageClick}
                        handlePreviousPageClick={this.props.handlePreviousPageClick}
                        handleNextPageClick={this.props.handleNextPageClick}
                    />
                </Row>
            </Container>
        );
    }
};

export default TaskDetailView;
