import React from 'react'
import CardLinkApp from '../../CardLinkApp';

const StationManagerCardView = () => {
    return (
        <CardLinkApp 
            to="/qst/stationsmanager"
            imgPath="img/iconImg/workBench.png"
            cardText="Manage stations controller and connected stations."
            cardTitle="Stations manager"
            alt="Work Benchs"
        />
    )
}

export default StationManagerCardView;

