import React from 'react'
import CardLinkApp from '../../CardLinkApp';

const TasksCardView = () => {
    return (
        <CardLinkApp 
            to="/qst/task"
            imgPath="img/iconImg/tasksTraceability.png"
            cardText="Access the list of registered jobs and create new tasks."
            cardTitle="Tasks"
            alt="Tasks QST"
        />
    )
}

export default TasksCardView;

